<template>
    <transition name="fade">
        <div class="insura">
            <div class="insura__main" v-show="!insuraMore">
                <div class="cap-top rclockwise"></div>
                <div class="cap-bottom clockwise"></div>
                
                <!-- 直接对容器进行淡入淡出 -->
                <ul :class="['globe', 'trans_flag', { fade_flag: fadeFlag }]">
                    <li class="globe__item"
                     v-for="(item, i) in newArr" :key="i"
                     @click="hideMarket(i)">{{ item.typename }}</li>
                </ul>

                <div class="change" @click="replace"></div>
            </div>

            <transition name="fade">
                <div class="insura__ways" v-show="insuraMore">
                    <!-- 引入search -->
                    <search></search>

                    <div class="ways__header">{{ Market[3].typearr[globeIdx].typename }}</div>

                    <ul class="ways__more">
                        <li class="ways__more__item"
                         v-for="(item, i) in Market[3].typearr[globeIdx].typeintro" :key="i"
                         @click="showDetail(i)">
                            <div class="ways__more__item-title">{{ item.introname }}</div>
                            <ul class="ways__more__item__btnct">
                                <li class="ways__more__item__btnct-btn ways-btn1">理财型</li>
                                <li class="ways__more__item__btnct-btn ways-btn2">期缴</li>
                                <li class="ways__more__item__btnct-btn ways-btn3">泰康人寿</li>
                            </ul>
                            <div class="ways__more__item-buycar"></div>
                        </li>
                    </ul>

                    <!-- <div class="return" @click="back">
                        <div class="return__btn"><img src="../assets/foot/back@2.png" alt=""></div>
                        <div class="return__text">返回</div>
                    </div>                     -->
                </div>
            </transition>

            <transition name="fade">
                <div class="insura__detailct" v-show="details">
                    <div class="insura__detail">
                        <div class="insura__detail__close" @click="closeDetail"></div>
                        <ul class="insura__detail__intro">
                            <li class="insura__detail__intro-series">{{ Market[3].typearr[5].typeintro[detailIdx].itos }}</li>
                            <li class="insura__detail__intro-type">{{ Market[3].typearr[5].typeintro[detailIdx].introtype }}</li>
                            <li class="insura__detail__intro-top">》保险公司名称</li>
                            <li class="insura__detail__intro-bottom">{{ Market[3].typearr[5].typeintro[detailIdx].introico }}</li>
                            <li class="insura__detail__intro-top">》产品类别</li>
                            <li class="insura__detail__intro-bottom">{{ Market[3].typearr[5].typeintro[detailIdx].introtype }}</li>
                            <li class="insura__detail__intro-top">》投保年龄范围</li>
                            <li class="insura__detail__intro-bottom">{{ Market[3].typearr[5].typeintro[detailIdx].introage }}</li>
                            <li class="insura__detail__intro-top">》保障期限</li>
                            <li class="insura__detail__intro-bottom">{{ Market[3].typearr[5].typeintro[detailIdx].introlimit }}</li>
                            <li class="insura__detail__intro-top">》缴费方式</li>
                            <li class="insura__detail__intro-bottom">{{ Market[3].typearr[5].typeintro[detailIdx].intropayment }}</li>
                            <li class="insura__detail__intro-top">》缴费年限</li>
                            <li class="insura__detail__intro-bottom">{{ Market[3].typearr[5].typeintro[detailIdx].introyears }}</li>
                        </ul>

                        <div class="insura__spaceline"></div>

                        <metal-code></metal-code>
                    </div>
                </div>
            </transition>           
        </div>
    </transition>
</template>
<script>
import { mapGetters } from 'vuex'
// import {mapGetters, mapActions} from 'vuex'
import search from './search.vue'
import metalCode from './market-metal-code.vue'

export default {
    components: {
        search,
        metalCode
    },
    computed: {
        ...mapGetters([
            'Market'
        ])
    },
    props: [
        'msg'
    ],
    data() {
        return {
            forIdx: 0,
            globeIdx: 0,
            detailIdx: 0,
            details: false,
            insuraMore: false,
            senior: false,
            newArr: [],
            fadeFlag: true
        }
    },
    methods: {
        replace() {
            let insurance =  this.Market[3].typearr;

            this.fadeFlag = false;
            setTimeout(() => {
                if(this.forIdx >= insurance.length) {
                    this.forIdx = 0;
                    this.newArr = insurance.slice(this.forIdx, this.forIdx + 6);
                    // console.log(this.newArr)
                } else {
                    this.newArr = insurance.slice(this.forIdx, this.forIdx + 6);
                    this.forIdx += 6;
                }
                this.fadeFlag = true;
            }, 500);
            
        },
        hideMarket(idx) {
            // 保存当前索引
            this.globeIdx = idx;
            // 隐藏父组件导航
            this.msg.show = !this.msg.show;
            // 展示子组件模板
            this.insuraMore = !this.insuraMore;
            // console.log(this.insuraMore)
        },
        showDetail(idx) {
            this.detailIdx = idx;
            this.details = !this.details;
        },
        closeDetail() {
            this.details = !this.details;
        },
        showSenior() {
            this.senior = !this.senior;
        },
        closeSenior() {
            this.senior = !this.senior;
        },
        back() {
            this.msg.show = !this.msg.show;
            this.insuraMore = !this.insuraMore;
        }
    },
    created() {
        this.replace();
    }
}
</script>
<style scoped>
    @keyframes clockwise {
        from,to {
            transform: skewX(20deg);
            /* transform: rotateY(0); */
        }
        40% {
            transform: skewX(50deg);
            /* transform: rotateY(360deg);  */
        }
        80% {
            transform: skewX(40deg);
        }
    }
    @keyframes rclockwise {
        from,to {
            transform: skewX(-20deg);
            /* transform: rotateY(0); */
        }
        40% {
            transform: skewX(-50deg);
            /* transform: rotateY(360deg);  */
        }
        80% {
            transform: skewX(-40deg);
        }
    }

    .clockwise {
        animation: 10s clockwise infinite;
    }
    .rclockwise {
        animation: 10s rclockwise infinite;
    }

    /* 重置组件样式 */
    .search {
        position: absolute;
        top: 0.875rem;
        right: 0.8594rem;
    }
    /* 返回按钮 */
    .return {
        position: absolute;
        z-index: 11;
        bottom: 0.0785rem;
        left: 0.1174rem;
        width: 0.349rem;
        text-align: center;
        font-size: 0.0833rem;
        /* text-shadow: 0rem 0rem 0.2188rem #96BDFF, 0rem 0rem 0.375rem #96BDFF, 0rem 0rem 0.7813rem #96BDFF; */
    }
    .return__btn {
        width: 0.349rem;
        height: 0.3125rem;
    }
    /* 淡入淡出 */
    .trans_flag {
        opacity: 0;
        transition: all .3s;
    }
    .fade_flag {
        opacity: 1!important;
    }


    .insura {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        font-family: 'PingFangSC-Semibold';
        font-weight: 600;
        color: rgba(223,239,255,1);
    }
    .insura__main {
        position: relative;
    }

    .cap-top {
        position: absolute;
        top: 2.0677rem;
        left: 3.3073rem;
        width: 5.5573rem;
        height: 1.5573rem;
        background: url('../assets/market/insurance/topcap.png') no-repeat;
        background-size: contain;
    }
    .cap-bottom {
        position: absolute;
        top: 3.0156rem;
        left: 2.1979rem;
        width: 7.5729rem;
        height: 1.2865rem;
        background: url('../assets/market/insurance/bottomcap.png') no-repeat;
        background-size: contain;        
    }
    .globe {
        position: relative;
    }
    .globe__item {
        position: absolute;
        background: no-repeat;
        background-size: contain;
        text-align: center;
        opacity: 1;
    }
    .globe__item:nth-of-type(1) {
        top: 1.3854rem;
        left: 4.5625rem;
        width: 0.8854rem;
        line-height: 0.8854rem;
        background-image: url('../assets/market/insurance/1.png') ;

    }
    .globe__item:nth-of-type(2) {
        top: 1.4948rem;
        left: 6.625rem;
        width: 0.6979rem;
        line-height: 0.6979rem;
        font-size: 0.0625rem;
        background-image: url('../assets/market/insurance/2.png');
    }
    .globe__item:nth-of-type(3) {
        top: 1.8906rem;
        left: 8.0833rem;
        width: 1.0521rem;
        line-height: 1.0521rem;
        font-size: 0.1042rem;
        background-image: url('../assets/market/insurance/3.png');
    }
    .globe__item:nth-of-type(4) {
        top: 2.7344rem;
        left: 6.6875rem;
        width: 1.375rem;
        line-height: 1.375rem;
        font-size: 0.1563rem;
        background-image: url('../assets/market/insurance/4.png');
    }
    .globe__item:nth-of-type(5) {
        top: 2.7604rem;
        left: 4.0938rem;
        width: 1.5885rem;
        line-height: 1.5885rem;
        font-size: 0.1875rem;
        background-image: url('../assets/market/insurance/5.png');
    }
    .globe__item:nth-of-type(6) {
        top: 1.8385rem;
        left: 2.6667rem;
        width: 1.2708rem;
        line-height: 1.2708rem;
        font-size: 0.125rem;
        background-image: url('../assets/market/insurance/6.png');
    }

    .change {
        position: absolute;
        top: 4.349rem;
        left: 8.7552rem;
        width: 0.6823rem;
        height: 0.276rem;
        background: url('../assets/market/insurance/change.png') no-repeat;
        background-size: contain;
    }


    .insura__ways {
        position: relative;
        /* top: 0;
        right: 0;
        bottom: 0;
        left: 0; */
        /* z-index: 11; */
        width: 10rem;
        height: 5.625rem;
        box-sizing: border-box;
        padding: 1.1563rem 0.4688rem 1.2448rem 0.8594rem;
        font-family: 'PingFangSC-Medium';
    }
    .ways__seniorbtn {
        position: absolute;
        top: 0.875rem;
        left: 8.5417rem;
        width: 0.5833rem;
        height: 0.2813rem;
        background: url('../assets/homeland/main/gaoji.png') no-repeat;
        background-size: contain;
    }    
    .ways__header {
        position: absolute;
        top: 0;
        left: 0;
        width: 10rem;
        height: 0.5052rem; 
        background: url('../assets/common/Group13@2x.png') no-repeat;
        background-size: contain;
        font-size: 0.2083rem;
        font-weight: 500;
        color: rgba(223,239,255,1);
        line-height: 0.5052rem;
        text-shadow: 0px 0px 0.0469rem rgba(150,189,255,1);
        text-align: center;
    }
    .ways__more {
        display: flex;
        flex-wrap: wrap;
    }
    .ways__more__item {
        position: relative;
        flex-shrink: 0;
        float: left;
        width: 2.5521rem;
        height: 1.2396rem;
        margin-top: 0.3958rem;
        margin-right: 0.3385rem;
        background: url('../assets/market/insurance/Group6@2x.png') no-repeat;
        background-size: contain;

        font-size: 0.1042rem;
        line-height: 0.1458rem;
        text-shadow: 0rem 0rem 0.0573rem rgba(150,189,255,1);
    }
    .ways__more__item-title {
        display: flex;
        justify-content: flex-end;
        margin-top: 0.0365rem;
        margin-right: 0.25rem;
    }
    .ways__more__item__btnct {
        display: flex;
        box-sizing: border-box;
        padding: 0.2865rem 0.0885rem 0.1615rem 0.2188rem;
    }
    .ways__more__item__btnct-btn {
        margin-right: 0.0885rem;
        background-repeat: no-repeat;
        background-size: contain;
        line-height: 0.2448rem;
        text-align: center;
    }
    .ways-btn1 {
        width: 0.5938rem;
        background-image: url('../assets/market/insurance/理财型宽@2x.png');
    }
    .ways-btn2 {
        width: 0.4844rem;
        background-image: url('../assets/market/insurance/Rectangle13Copy@2x.png');
    }
    .ways-btn3 {
        width: 0.6927rem;
        background-image: url('../assets/market/insurance/Rectangle14Copy2@2x.png');
    }
    .ways__more__item-buycar {
        width: 1.026rem;
        height: 0.2396rem;
        margin-left: 0.6875rem;
        background: url('../assets/market/insurance/购物车@2x.png') no-repeat;
        background-size: contain;
    }

        /* 高级搜索 */
    .insura__senior {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 11;
        width: 3.901rem;
        height: 5.625rem;
        box-sizing: border-box;
        padding-left: 0.5rem;
        background: url('../assets/homeland/senior/seniorbg.png') no-repeat;
        background-size: contain;
    }
    .senior__close {
        position: absolute;
        left: 0;
        top: 42%;
        width: 0.2604rem;
        height: 0.4688rem;
        background: transparent;
    }


    /**
    *弹层
    */
    .insura__detailct {
        position: absolute;
        z-index: 12;
        top: 0;
        left:0;
        right: 0;
        height: 5.625rem;
        background: rgba(16,16,16,.5);
    }
    .insura__detail {
        display: flex;
        position: absolute;
        top: 0.9531rem;
        left: 1.2448rem;
        width: 7.5208rem;
        height: 3.7292rem;
        background: url('../assets/market/invest/Groupbg.png') no-repeat;
        background-size: contain;

        box-sizing: border-box;
        padding: 0.3438rem 0.099rem 0 0.7552rem;
        font-family: 'PingFangSC-Medium';
        font-size: 0.1302rem;
        line-height: 0.1302rem;
    }
    .insura__detail__close {
        position: absolute;
        top: 0.0729rem;
        right: 0.0833rem;
        width: 0.401rem;
        height: 0.401rem;
        background: url('../assets/homeland/detail/detailclose.png') no-repeat;
        background-size: contain;
    }

    .insura__detail__intro-series {
        font-size:0.1563rem;
        line-height: 0.2188rem;
        text-shadow: 0rem 0rem 0.0417rem rgba(150,189,255,1);
    }
    .insura__detail__intro-type {
        margin-top: 0.0573rem;
        margin-bottom: 0.0781rem;
        font-size: 0.0938rem;
    }
    .insura__detail__intro-top {
        margin-top: 0.125rem;
        font-size:0.1042rem;
        line-height:0.1458rem;
    }
    .insura__detail__intro-bottom {
        margin-top: 0.026rem;
        font-size: 0.0938rem;
        color:rgba(95,224,235,1);
        text-indent: 0.0938rem;
    }

    .insura__spaceline {
        width: 0.0156rem;
        height: 3.0365rem;
        margin-left: 3.2865rem;
        background: url('../assets/market/metal/cover-line.png') no-repeat;
        background-size: contain;
    }
</style>



