<template>
    <!-- 基金产品详情 -->
    <transition name="fade">
        <div class="fund__details--wrapper">
            <div class="fund__details__box">
                <div class="fund__details__box__chart">
                    <div class="wave"></div>
                    <div class="fund__detail__side-per">4.05%</div>
                </div>

                <div class="fund__details__box__desc">
                    <div class="fund__details__box__desc--title">{{ details.title }}</div>
                    <div class="fund__details__box__desc--wrapper">
                        <div class="fund__details__box__desc--item">预期年化收益率：<span class="color">4.05%</span></div>
                        <div class="fund__details__box__desc--item">产品编号：{{ details.code }}</div>
                        <div class="fund__details__box__desc--item">交易类型：{{ details.Cdate }}</div>
                        <div class="fund__details__box__desc--item">币种：{{ details.type }}</div>
                        <div class="fund__details__box__desc--item">基金状态：{{ details.state }}</div>
                        <div class="fund__details__box__desc--item">币种：{{ details.currency }}</div>
                        <div class="fund__details__box__desc--item">募集期：{{ details.netNum }}</div>
                        <div class="fund__details__box__desc--item">收益特点：{{ details.grade }}</div>
                        <div class="fund__details__box__desc--item ">起购金额：{{ details.Sdate }}</div>
                        <div class="fund__details__box__desc--item">递增金额：{{ details.Mrate }}</div>
                        <div class="fund__details__box__desc--item">预计到期日：{{ details.Hrate }}</div>
                        <div class="fund__details__box__desc--item">近一年净值增长率：{{ details.Yrate }}</div>
                        <div class="fund__details__box__desc--item">收益支付方式：{{ details.Ndate }}</div>
                    </div>
                    <div class="fund__details__box__Ccltor">
                        <div class="fund__details__box__Ccltor__title">基金计算器</div>
                        <div class="fund__details__box__Ccltor--wrapper">
                            <div class="Ccltor--add--wrapper">
                                <div class="fund__details__box__Ccltor--frame">
                                    <div class="add"></div>
                                </div>
                                <div class="fund__details__box__Ccltor--txt">手续费试算</div>
                            </div>
                            <div class="Ccltor--rate--wrapper">
                                <div class="fund__details__box__Ccltor--frame">
                                    <div class="rate"></div>
                                </div>
                                <div class="fund__details__box__Ccltor--txt">收益试算</div>
                            </div>                    
                        </div>
                            
                    </div>
                </div>
                <div class="cover-line"></div>
                <metalcode class="metalcode"></metalcode>   
                <div class="fund__details__box__close" @click="back"></div>
            </div>
        </div>
    </transition>
</template>
<script>
import { mapGetters } from 'vuex'
import metalcode from './market-metal-code.vue'

export default {
    computed: {
        ...mapGetters([
            'FundProduct'
        ])
    },
    props: ['detail'],
    components: {
        metalcode
    },
    data() {
        return {
            isShow: false,
            // 产品详情
            details: {}
        }     
    },
    methods: {
        // 打开详情
        proDet(idx) {
            // 点击的产品详情数据
            this.details = this.FundProduct[idx].details;
        },
        back() {
            this.detail.detail = ! this.detail.detail;
        }
    }
}
</script>
<style scoped>
    @keyframes wave {
        from {
            transform: rotate(0deg);
        }
        from {
            transform: rotate(360deg);
        }
    }

    /* 产品详情弹窗 */
    .fund__details--wrapper {
        /* transition: .1s all; */
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        width: 10rem;
        height: 5.625rem;
        background-color: rgba(0, 0, 0, .5);
    }
    .fund__details__box {
        position: absolute;
        left: 1.2448rem;
        top: 0.9531rem;
        /* transform: translate(-50%, -50%); */
        width: 7.3646rem;
        height: 3.5573rem;
        background: url('../assets/market/invest/Groupbg.png') no-repeat;
        background-size: 100% 100%;
        box-sizing: border-box;
        padding: 0 0 0.1042rem 0.2448rem;
        display: flex;
        align-items: center;
    }
    /* 图表 */
    .fund__details__box__chart {
        display: inline-block;
        width: 2.0625rem;
        height: 2.9427rem;
        background: linear-gradient(180deg,rgba(39,57,142,1) 0%,rgba(7,18,71,1) 100%);
        /* margin-top: 0.3281rem; */
    }

    .fund__details__box__close {
        position: absolute;
        top: 0.0729rem;
        right: 0.0833rem;
        width: 0.401rem;
        height: 0.401rem;
        background: url('../assets/market/fund/inside_close@2x.png') no-repeat;
        background-size: contain;
    }

    .fund__details__box__desc {
        padding: 0 0.2188rem;
        color: #DFEFFF;
        width: 3.3854rem;
    }
    .fund__details__box__desc--title {
       
        font-size: 0.1563rem;
        line-height: 0.2188rem;
        text-shadow: 0 0 0.0417rem rgba(150,189,255,1);
        padding-bottom: 0.2188rem;
    }
    .fund__details__box__desc--wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        font-size: 0.0938rem;
        font-weight: 500;
        height: 1.4063rem;
    }
    .fund__details__box__desc--item:nth-of-type(2n-1) {
        padding-left: 0.2188rem;
    }
    .fund__details__box__desc--item:nth-of-type(1) {
        flex: 0 0 100%;
        padding-left: 0;
    }
    .fund__details__box__desc--item {
        box-sizing: border-box;
        flex: 0 0 50%;
        line-height: 0.1719rem;
    }
    .metalcode {
        box-sizing: border-box;
        margin-left: 0;
    }

    /* 基金计算器 */
    .fund__details__box__Ccltor__title {
        font-size: 0.125rem;
        line-height: 0.4167rem;
    }
    .fund__details__box__Ccltor--wrapper {
        display: flex;

    }
    .Ccltor--rate--wrapper,
    .Ccltor--add--wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .Ccltor--add--wrapper {
        padding-right: 0.1563rem;
    }
    .fund__details__box__Ccltor--frame {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 0.4792rem;
        height: 0.4792rem;
        background: url('../assets/market/fund/Group25@2x.png') no-repeat;
        background-size: contain;
    }
    .fund__details__box__Ccltor--frame .add {
        width: 0.25rem;
        height: 0.25rem;
        background: url('../assets/market/fund/计算器@2x.png') no-repeat;
        background-size: contain;
    }
    .fund__details__box__Ccltor--frame .rate {
        width: 0.25rem;
        height: 0.25rem;
        background: url('../assets/market/fund/收益计算@2x.png') no-repeat;
        background-size: contain;
    }
    .fund__details__box__Ccltor--txt {
        font-size: 0.0938rem;
        line-height: 0.2865rem;
    }
    .cover-line {
        width: 0.0156rem;
        height: 3.0365rem;
        margin-right: 0.0521rem;
        margin-top: 0.5rem;
        background: url('../assets/market/fund/cover-line.png') no-repeat;
        background-size: contain;
    }

    /* 返回按钮 */
    .return {
        position: absolute;
        z-index: 11;
        bottom: -0.8073rem;
        left: -1.875rem;
        width: 0.349rem;
        text-align: center;
        font-size: 0.0833rem;
    }
    .return__btn {
        width: 0.349rem;
        height: 0.3125rem;
    }

    .color {
        font-size:0.1458rem;
        font-family: 'PingFangSC-Medium';
        font-weight:500;
        color:rgba(99,239,246,1);
        line-height:0.2083rem;
    }
    /* 海浪效果 */
    .fund__details__box__chart {
        position: relative;
        width: 2.0625rem;
        height: 2.9427rem;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        padding: 0.2917rem 0.6563rem;
        background: rgba(39,103,142,1);
    }
    .wave {
        width: 800px;
        height: 800px;
        position: absolute;
        top: -2.9rem;
        left: -0.9rem;
        border-radius: 35%;
        background: rgba(39,57,142,1);
        -webkit-animation: wave 15s infinite linear;
        animation: wave 15s infinite linear;
    }
    .fund__detail__side-per {
        position: absolute;
        z-index: 1;
        width: 0.75rem;
        line-height: 0.75rem;
        background: url('../assets/market/transfer/left@2x.png') no-repeat;
        background-size: contain;
        text-align: center;
        font-size: 0.125rem;
        text-shadow: 0rem 0.0104rem 0.0208rem rgba(0,0,0,0.5);
    }
</style>



