<template>
    <div class="index">
        <bulletin :shop="shop"></bulletin>
        <cirque ref="cirqueData"></cirque>
    </div>
</template>
<script>
import cirque from './cirque.vue';
import bulletin from './bulletin.vue';

export default {
    components: {
        'cirque': cirque,
        'bulletin': bulletin,
    },
    mounted: function(){
        this.shop = this.$refs.cirqueData.sendData();
    },
    data() {
        return {
        shop: []
        }
    },
}
</script>
<style>

</style>




