<template>
<transition name="fade">
    <div class="wrapper" v-show="isShow">
        <div class="wish">
            <div class="wish__header">
                <div class="wish__header__mode" @click="toggle">
                    <div class="wish__header__template" :class="[isActive ? 'active' : '']">{{ this.mode.template }}</div>
                    <div class="wish__header__custom" :class="[!isActive ? 'active' : '']">{{ this.mode.custom }}</div>
                </div>
                <div class="wish__header__close" @click="isShow=false"></div>
            </div>
        <transition-group name="fade">
            <tmp v-show="isActive" key="tmp"></tmp>
            <ctm v-show="!isActive" key="ctm"></ctm>
        </transition-group>
        </div>
    </div>
</transition> 
</template>
<script>
import tmp from './wish-template.vue'
import ctm from './wish-custom.vue'

export default {
    components: {
        tmp,
        ctm
    },
    props: {
        value: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            mode: {
                template: '心愿模板',
                custom: '自定义',
            },
            isShow: this.value,
            isActive: true,
        }
    },
    methods: {
        toggle(e) {
            let target = e.target;
            if (target.classList[0] === 'wish__header__template') {
                this.isActive = true;
            } else  if (target.classList[0] === 'wish__header__custom') {
                this.isActive = false;
            }
        }
    },
    watch: {
        value: function (val) {
            this.isShow = val;
        },
        isShow: function (val) {
            this.$emit('input', val);
        }
    }
}
</script>
<style scoped>
    /* 返回按钮过渡动画 */
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
    .wrapper {
        position: absolute;
        left: 0;
        top: 0;
        width: 10rem;
        height: 5.625rem;
        background-color: rgba(0, 0, 0, .5);
    }
    /* 许愿弹窗 */
    .wish {
        position: absolute;
        padding-top: 0.8073rem;
        box-sizing: border-box;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 6.8021rem;
        height: 3.8333rem;
        background: url('../assets/wish/Group50@2x.png') no-repeat;
        background-size: contain;
    }
    .wish__header {
        position: absolute;
        top: 0;
        width: 6.7813rem;
        height: 0.5521rem;
        background: url('../assets/wish/Group48@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    /* 切换按钮 */
    .wish__header__mode {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        color: #DFEFFF;
    }
    .wish__header__custom,
    .wish__header__template {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 0.1042rem;
        width: 0.9375rem;
        height: 0.2708rem;
        background: url('../assets/wish/Rectangle17Copy2@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .active {
        background:linear-gradient(180deg,rgba(84,173,245,1) 0%,rgba(30,95,189,1) 100%);
    }
    .wish__header__close {
        position: absolute;
        right: 0.0885rem;
        top: 0.1771rem;
        width: 0.1354rem;
        height: 0.1354rem;
        background: url('../assets/common/Group8@2x.png') no-repeat;
        background-size: contain;
    }
</style>




