<template>
<transition name="fade">
    <div class="wrapper" v-show="isShow">
        <div class="clr">
            <div class="clr__header">
                <div class="clr__header__title">{{ this.title }}</div>
                <div class="clr__header__light"></div>
                <div class="clr__header__close" @click="isShow=false"></div>
            </div>
            <div class="clr__body">
                
                <ul class="clr__mode">
                    <li class="clr__mode__item"  v-for="(mode, i) in modes" :key="i" :class="[i === nowIdx ? 'active' : '']"  @click="toggle(i)">{{ mode }}</li>
                </ul>
                <inter v-show="nowIdx === 0 ? true : false"></inter>
                <loan v-show="nowIdx === 1 ? true : false"></loan>
                <repayment v-show="nowIdx === 4 ? true : false"></repayment>
                <div class="clr__prompt">{{ prompt }}</div>
                <div class="clr__button">
                    <div class="clr__button--clear">清除</div>
                    <div class="clr__button--calculate">计算</div>
                </div>
            </div>
        </div>
    </div>
</transition>
</template>
<script>
import inter from './cal-interest.vue'
import loan from './cal-loan.vue'
import repayment from './cal-repayment.vue'

export default {
    components: {
        inter,
        loan,
        repayment
    },
    props: {
        value: {
            type: Boolean,
            default: false,
        }
    },
    data() {
        return {
            title: '计算器',
            isShow: this.value,
            modes: ['存款利息', '贷款', '组合贷款', '外币兑换', '提前还款'],
            nowIdx: 0,
            prompt: '计算器不考虑利息变更，一律按照最新利息税计算'
        }     
    },
    methods: {
        toggle(index) {
            this.nowIdx = index;
        }
    },
    computed: {

    },
    watch: {
        value: function (val) {
            this.isShow = val;
        },
        isShow: function (val) {
            this.$emit('input', val);
        }
    }
}
</script>
<style scoped>
    /* 返回按钮过渡动画 */
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
    .wrapper {
        position: absolute;
        left: 0;
        top: 0;
        width: 10rem;
        height: 5.625rem;
        background-color: rgba(0, 0, 0, .5);
    }
    .clr {
        position: absolute;
        box-sizing: border-box;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 5.4531rem;
        /* height: 3.3542rem; */
        background: url('../assets/common/Rectangle6@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .clr__header {
        position: absolute;
        top: 0;
        width: 100%;
        height: 0.4896rem;
        background: url('../assets/call/Group36@2x.png') no-repeat;
        background-size: 100% 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .clr__header__title {
        color: #fff;
        font-size: 0.1458rem;
    }
    .clr__header__light {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 1.3333rem;
        height: 0.2396rem;
        background: url('../assets/call/Oval11@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .clr__header__close {
        position: absolute;
        right: 0.0885rem;
        top: 0.1771rem;
        width: 0.1354rem;
        height: 0.1354rem;
        background: url('../assets/common/Group8@2x.png') no-repeat;
        background-size: contain;
    }
    /*  */
    .clr__body {
        display: flex;
        /* justify-content: space-around; */
        /* flex-wrap: wrap; */
        flex-direction: column;
        align-items: center;
        color: #DFEFFF;
        font-size: 0.0833rem;
        padding: 0 0.3125rem;
        padding-bottom: 0.3333rem;
    }
    .clr__mode {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin-top: 0.6927rem;
        width: 100%;
    }
    .clr__mode__item {
        font-size: 0.0833rem;
        text-align: center;
        line-height: 0.1875rem;
        width: 0.6198rem;
        height: 0.1875rem;
        background: url('../assets/calculator/Rectangle17@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .active {
        background:linear-gradient(180deg,rgba(84,173,245,1) 0%,rgba(30,95,189,1) 100%);
    }
    /* 提示 */
    .clr__prompt {
        font-size: 0.0729rem;
        line-height: 0.2rem;
        color: #C3D4E7;
    }
    /* 按钮 */
    .clr__button {
        display: flex;
        width: 2.2656rem;
        justify-content: space-between;
        margin-top: 0.3125rem;
    }
    .clr__button--clear,
    .clr__button--calculate {
        width: 1.026rem;
        height: 0.3125rem;
        font-size: 0.125rem;
        line-height: 0.3125rem;
        text-align: center;
    }
    .clr__button--clear {
        background: url('../assets/common/Group20@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .clr__button--calculate {
        background: url('../assets/common/Group201@2x.png') no-repeat;
        background-size: 100% 100%;
    }
</style>





