<template>
    <div class="message">
        <h-header :title="this.title"></h-header>
        <div class="message__wrapper">
            <search></search>
            <div class="message__map">
                <img src="../assets/message/Bitmap@2x.png">
            </div>
            <div class="message__forecast">
                <div class="message__forecast__title">{{ this.forecast.title }}</div>
                <div class="message__forecast__graded">
                    <div class="message__forecast__graded--free"><i></i>空闲</div>
                    <div class="message__forecast__graded--commonly"><i></i>一般</div>
                    <div class="message__forecast__graded--busy"><i></i>繁忙</div>
                </div>
                <div class="message__forecast__week">
                    <div class="message__forecast__week__wrapper" v-for="(week, key) in forecast.weeks" :key="key">
                        <div class="message__forecast__week--day">{{week.day}}</div>
                        <div class="message__forecast__week--rate" ref="rate">{{week.rate}}</div>
                    </div>
                </div>
                <div class="message__forecast__prompt">{{ this.forecast.prompt }}</div>
            </div>
            <div class="message__branch">
                <div class="message__branch__title">{{ this.branch.title }}</div>
                <div class="message__branch__address">地址：{{ this.branch.address }}</div>
                <div class="message__branch__call">联系方式：{{ this.branch.call }}</div>
                <div class="message__branch__rate">月均准点率：{{ this.branch.ptyRate }}</div>
                <div class="message__branch__time">
                    <div class="message__branch__time--desc">周一至周五营业时间：</div>
                    <div class="message__branch__time--working">
                        <div class="message__branch__time--workingDayS">对私：{{ this.branch.time.workingDayS }}</div>
                        <div class="message__branch__time--workingDayG">对公：{{ this.branch.time.workingDayG }}</div>
                    </div>
                    <div class="message__branch__time--saturday">周六营业时间：{{ this.branch.time.saturday }}</div>
                    <div class="message__branch__time--sunday">周日营业时间：{{ this.branch.time.sunday }}</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import header from './h-header.vue'
import search from './search.vue'

export default {
    components: {
        'h-header': header,
        search,
    },
    data() {
        return {
            title: '网点信息查询',
            branch: {
                    title: '建设银行成都支行',
                    address: '四川省成都市天府广场',
                    call: '82066788',
                    time: {
                        workingDayS: '09:00:00-17:00:00',
                        workingDayG: '09:00:00-12:00:00 09:00:00-12:00:00',
                        saturday: '09:00:00-17:00:00',
                        sunday: '09:00:00-17:00:00'
                    },
                    ptyRate: '100%'
            },
            forecast: {
                title: '未来7天繁忙程度预测',
                prompt: '百分比代表准点率，客户等待20分钟以内为准点',
                weeks: [
                    {day: '20日', rate: '12%'},
                    {day: '21日', rate: '43%'},
                    {day: '22日', rate: '20%'},
                    {day: '23日', rate: '25%'},
                    {day: '24日', rate: '55%'},
                    {day: '25日', rate: '98%'},
                    {day: '26日', rate: '90%'},
                ]
            }
           
        }
    },
    mounted() {
        let foo = this.$refs.rate;
        foo.forEach((el) => {
            let rate = parseInt(el.innerHTML);
            if (rate > 30 && rate < 70) {
                el.classList.add('message__forecast__week--rate--commonly');
            } else if (rate > 70) {
                el.classList.add('message__forecast__week--rate--busy');
            }
            
        })
    },
    methods: {
      

    }

}
</script>
<style scoped>
    .message {
        width: 10rem;
        height: 5.625rem;
        /* background-color: rgba(0, 0, 0, .5); */
    }
    .message__wrapper {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 7.3646rem;
        height: 3.6875rem;
        background: url('../assets/message/zhongzhong@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    /* 搜索 */
    .search {
        position: absolute;
        right: 0.5469rem;
        top: 0.3385rem;
    }
    .message__map {
        position: absolute;
        top: 0.7031rem;
        left: 0.4896rem;
        width: 3.6458rem;
        height: 2.5521rem;
    }
    .message__map img {
        width: 100%;
        height: 100%;
    }
    /* 网点情况 */
    .message__forecast {
        position: absolute;
        top: 0.776rem;
        right: 0.5469rem;
        width: 2.5208rem;
        height: 0.8385rem;
        background: url('../assets/message/Mask@2x.png') no-repeat;
        background-size: contain;
        padding: 0.1042rem;
        box-sizing: border-box;
    }
    .message__forecast__graded,
    .message__forecast__title {
        color: #DFEFFF;
        font-size: 0.125rem;
        display: inline-block;
        vertical-align: middle;
    }
    .message__forecast__graded--free,
    .message__forecast__graded--commonly,
    .message__forecast__graded--busy {
        display: inline-block;
        vertical-align: middle;
        margin-right: 0.0417rem;
    }
    .message__forecast__graded {
        font-size: 0.0625rem;
        margin-left: 0.1rem;
    }
    .message__forecast__graded i {
        margin-right: 0.0417rem;
        vertical-align: middle;
    }
    .message__forecast__graded--free i {
        width: 0.1042rem;
        height: 0.0625rem;
        background: url('../assets/message/Rectangle1@2x.png') no-repeat;
        background-size: contain;
    }
    .message__forecast__graded--commonly i {
        width: 0.1042rem;
        height: 0.0625rem;
        background: url('../assets/message/Rectangle2@2x.png') no-repeat;
        background-size: contain;
    }
    .message__forecast__graded--busy i {
        width: 0.1042rem;
        height: 0.0625rem;
        background: url('../assets/message/Rectangle3@2x.png') no-repeat;
        background-size: contain;
    }
    /* 每日的繁忙程度 */
    .message__forecast__week {
        display: flex;
        justify-content: space-between;
    }
    .message__forecast__week__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .message__forecast__week--day {
        font-size: 0.0729rem;
        color: #DFEFFF;
        line-height: 0.2rem;
    }
    .message__forecast__week--rate {
        font-size: 0.0625rem;
        width: 0.2708rem;
        height: 0.125rem;
        line-height: 0.125rem;
        text-align: center;
        color: #DFEFFF;
        background-image: url('../assets/message/RectangleBig1@2x.png');
        background-size: contain;
    }
    .message__forecast__week--rate--commonly {
        background-image: url('../assets/message/RectangleBig@2x.png');
        background-size: contain;
    }
    .message__forecast__week--rate--busy {
        background-image: url('../assets/message/RectangleBig2@2x.png');
        background-size: contain;
    }

    /* 提示信息 */
    .message__forecast__prompt {
        font-size: 0.0625rem;
        color: #DFEFFF;
        line-height: 0.25rem;
    }
    /* 网点信息 */
    .message__branch {
        color: #DFEFFF;
        font-size: 0.0729rem;
        line-height: 0.1615rem;
        position: absolute;
        top: 1.7708rem;
        right: 0.5208rem;
        width: 2.5833rem;
        height: 1.4427rem;
        background: url('../assets/message/MaskCopy@2x.png') no-repeat;
        background-size: contain;
        padding: 0.1563rem 0.1458rem;
        box-sizing: border-box;
    }
    .message__branch__title {
        font-size: 0.125rem;
        line-height: 0.1719rem;

    }
   
    .message__branch__rate,
    .message__branch__call {
        display: inline-block;
    }
    .message__branch__rate {
        padding-left: 0.5208rem;
    }
    .message__branch__time--desc,
    .message__branch__time--working {
        display: inline-block;
        vertical-align: top;
    }
    

</style>





