<template>
  <div id="app">
    <div class="main">
      <keep-alive>
        <transition :name="transitionName">   
          <router-view></router-view>
        </transition>
      </keep-alive>
      <m-footer></m-footer>
    </div>
   
  </div>
</template>
<script>
import index from './components/h-index.vue';
import shopping from './components/shopping.vue';
import message from './components/message.vue';
import market from './components/market.vue';
import footer from './components/footer.vue';
import homeland from './components/homeland.vue';

import {getFundList} from '@/api/api.js'
import {getRate} from '@/api/api.js'

export default {
  created() {
    // getFundList()
    //   .then((rs) => {
    //     this.obj = rs;
    //     console.log(this.obj);
    //   })
    //   .catch((e) => console.log(e))
    getRate().then((rs) => {
      console.log(rs);
    }).catch((e) => console.log(e))

  },
  methods: {
  
  },
  components: { 
    'index': index,
    'm-footer': footer,
    'shopping': shopping,
    'market': market,
    'message': message,
    'homeland': homeland
  },
  data(){
      return {
          transitionName:'',
          obj: ''
      }
  },
  watch: {//使用watch 监听$router的变化
    $route(to, from) {
      //如果to索引大于from索引,判断为前进状态,反之则为后退状态
      if(to.meta.index > from.meta.index){
        //设置动画名称
        this.transitionName = 'slide-left';
      }
      else{
        this.transitionName = 'slide-right';
      }
    }
  }
}
</script>
<style scoped>
.main {
  position: absolute;
  left: 0;
  top: 0;
 
  width: 10rem; 
  height: 5.625rem;
  overflow: hidden;
  background: url('./assets/index/bg@2x.png') no-repeat;
  background-size: cover;
  user-select: none;
 
}

.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 500ms;
  position: absolute;
}
.slide-right-enter {
  opacity: 0;
  transform: translate(-100%, 0);
}
.slide-right-leave-active {
  opacity: 0;
  transform: translate(20%, 0);
}
.slide-left-enter {
  opacity: 0;
  transform: translate(20%, 0);
}
.slide-left-leave-active {
  opacity: 0;
  transform: translate(-100%, 0);
}

</style>



