<template>
<transition name="fade">
    <div class="wrapper" v-show="isShow">
        <div class="mine">
            <div class="mine__header">
                <div class="mine__header__title">{{ title }}</div>
                <div class="mine__header__light"></div>
                <div class="mine__header__close" @click="isShow=false"></div>
            </div>
            <div class="mine__body">
                <div class="mine__body__call">
                    <span>手机号</span>
                    <input type="text" :placeholder="inputDesc.name">
                </div>
                
                <div class="mine__body__confirm">确定</div>
            </div>
        </div>
    </div>
</transition>
</template>
<script>
export default {
    props: {
        value: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            inputDesc: {
                name: '输入手机号码',
                
            },
            title: '请输入您的手机号',
            isShow: this.value,
        }     
    },
    methods: {
        
    },
    watch: {
        value: function (val) {
            this.isShow = val;
        },
        isShow: function (val) {
            this.$emit('input', val);
        }
    }
}
</script>
<style scoped>
    /* 返回按钮过渡动画 */
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
    .wrapper {
        position: absolute;
        left: 0;
        top: 0;
        width: 10rem;
        height: 5.625rem;
        background-color: rgba(0, 0, 0, .5);
    }
    .mine {
        width: 3.5417rem;
        height: 1.9688rem;
        background: url('../assets/admin/Rectangle6@2x.png') no-repeat;
        background-size: 100% 100%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    .mine__header {
        position: absolute;
        top: 0;
        width: 100%;
        height: 0.4896rem;
        background: url('../assets/admin/Rectangle18@2x.png') no-repeat;
        background-size: 100% 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .mine__header__title {
        color: #fff;
        font-size: 0.1302rem;
    }
    .mine__header__light {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 1.3698rem;
        height: 0.3229rem;
        background: url('../assets/admin/Oval11@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .mine__header__close {
        position: absolute;
        right: 0.0885rem;
        top: 0.1771rem;
        width: 0.1354rem;
        height: 0.1354rem;
        background: url('../assets/common/Group8@2x.png') no-repeat;
        background-size: contain;
    }
    .mine__body {
        margin-left: 0.5469rem;
    }
    .mine__body span {
        display: inline-block;
        margin-right: 0.3385rem;
        color: #DFEFFF;
        font-size: 0.1042rem;
        width: 0.3125rem;
    }
    .mine__body__call {
        margin-top: 0.8854rem;
    }
    .mine__body__call input {
        box-sizing: border-box;
        font-size: 0.1rem;
        line-height: 0.2083rem;
        padding: 0 .08rem;
        width: 1.7708rem;
        height: 0.1875rem;
        background: url('../assets/admin/Rectangle10Copy2@2x.png') no-repeat;
        background-size: contain;
        color: #88DDFF;
        outline: none;
    }
    .mine__body__call input::placeholder {
        font-size: 0.0729rem;
    }
    .mine__body__confirm {
        width: 1.026rem;
        height: 0.3125rem;
        color: #DFEFFF;
        font-size: 0.1042rem;
        line-height: 0.3125rem;
        text-align: center;
        background: url('../assets/common/Group201@2x.png') no-repeat;
        background-size: contain;
        margin-top: 0.3333rem;
        margin-left: 0.7083rem;
    }
</style>



