<template>
    <div class="wrapper">
        <div class="foot">
            <transition name="fade">
                <div class="foot__back" @click="back" v-show="isShow">
                        <div class="foot__back__icon"><img src="../assets/foot/back@2.png"></div>
                        <div class="foot__back__txt">返回</div>
                </div>
            </transition>
            <div class="foot__wrapper">
                <div class="foot__item">
                    <div class="foot__item__icon--call"><img src="../assets/foot/down_small_icon1@2x.png"></div>
                    <div class="foot__item__txt--call">服务</div>
                </div>
                <div class="foot__item" @click="call">
                    <div class="foot__item__icon--call"><img src="../assets/foot/down_small_icon2@2x.png"></div>
                    <div class="foot__item__txt--call">呼叫查询</div>
                </div>
                <div class="foot__item" @click="card">
                    <div class="foot__item__icon--call"><img src="../assets/foot/down_small_icon3@2x.png"></div>
                    <div class="foot__item__txt--call">购物车</div>
                </div>
                <div class="foot__item" @click="wish">
                    <div class="foot__item__icon--call"><img src="../assets/foot/down_small_icon4@2x.png"></div>
                    <div class="foot__item__txt--call">心愿速递</div>
                </div>
                <div class="foot__item" @click="more">
                    <div class="foot__item__icon--call"><img src="../assets/foot/down_small_icon5@2x.png"></div>
                    <div class="foot__item__txt--call">更多</div>
                    <transition name="fade">
                        <ul class="dropdownWrapper" v-show='dropdownActive'>
                            <li @click="admin">
                                <img src="../assets/foot/admin@2x.png">
                                <span>管理员</span>
                            </li>
                            <li @click="mine">
                                <img src="../assets/foot/my@2x.png">
                                <span>我的</span>
                            </li>
                            <li @click="calculator">
                                <img src="../assets/foot/calculator@2x.png">
                                <span>计算器</span>
                            </li>
                        </ul>
                    </transition>
                </div>
            </div>
        </div>
        
        <call v-model="showCall" key="call"></call>
        <card v-model="showCard" key="card"></card>
        <wish v-model="showWish" key="wish"></wish>
        <admin v-model="showAdmin" key="admin"></admin>
        <mine v-model="showMine" key="mine"></mine>
        <calculator v-model="showCalculator" key="calculator"></calculator>
    </div>
    
</template>

<script>
import call from './call.vue'
import card from './shopingCard.vue'
import wish from './wish.vue'
import admin from './admin.vue'
import mine from './mine.vue'
import calculator from './calculator.vue'
 
export default {
    components: {
        call,
        card,
        wish,
        admin,
        mine,
        calculator,
    },
    data() {
        return {
            isShow: false,
            showCall: false,
            showCard: false,
            showWish: false,
            showAdmin: false,
            showMine: false,
            showCalculator: false,
            dropdownActive: false
        }
    },
    created() {
        if(this.$route.path === '/index') {
                this.isShow = false;
            } else{
                this.isShow = true;
        }
    },
    watch:{
        //监控路由变换，控制返回按钮的显示
        $route(now, old) {
            if (now.path ==="/index") {
                this.isShow = false;
            } else{
                this.isShow = true;
            }
        }
    },
    methods: {
        // 返回上一页
        back() {
            if (this.isShow) {
                this.$router.go(-1);
            }          
        },
        // 呼叫查询弹窗
        call() {
            this.showCall = true;
        },
        //购物车弹窗
        card() {
            this.showCard = true;
        },
        wish() {
            this.showWish = true;
        },
        // 更多
        more() {
            this.dropdownActive = !this.dropdownActive;
        },
        admin() {
            this.showAdmin = true;
        },
        mine() {
            this.showMine = true;
        },
        calculator() {
            this.showCalculator = true;
        }
    }
}
</script>
<style scoped>
    /* 返回按钮过渡动画 */
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
    .foot {
        position: absolute;
        top: 4.875rem;
        left: 0;
        width: 100%;
        height: 0.75rem;
        background: url('../assets/foot/down@2x.png') no-repeat;
        background-size: 100%;
        /* z-index: 0; */
    }
    .foot__wrapper {
        position: absolute;
        top: 0.3021rem;
        right: 1.1042rem;
        width: 2.6563rem;
        display: flex;
        justify-content: space-between;
    }
    .foot__item {
        display: flex;
        /* width: 0.3333rem; */
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 0.0833rem;
        color: #fff;
        text-shadow: 0rem 0rem 0.2188rem #96BDFF, 0rem 0rem 0.375rem #96BDFF,0rem 0rem 0.7813rem #96BDFF;
        letter-spacing: .015rem;
    }
    .foot__item__icon--call {
        width: 0.2188rem;
        height: 0.2188rem;
    }
    .foot__item__icon--call img {
        height: 100%;
        width: 100%;
    }
    .foot__item__txt--call {
        padding-top: 0.0417rem;
        text-align: center;
    }
    .foot__back {
        position: absolute;
        top: 0.2396rem;
        left: 0.125rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .foot__back__icon {
        width: 0.349rem;
        height: 0.3125rem;
    }
    .dropdownWrapper span,
    .foot__back__txt {
        font-size: 0.0833rem;
        color: #fff;
        line-height: 0.1146rem;
        text-shadow: 0rem 0rem 0.2188rem #96BDFF, 0rem 0rem 0.375rem #96BDFF,0rem 0rem 0.7813rem #96BDFF;
    }
    /* 更多菜单 */
    .dropdownWrapper {
        position: absolute;
        bottom: .4rem;
        /* transition: all 1s; */
        
    }
    .dropdownWrapper li {
        margin-bottom: 0.0521rem;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .dropdownWrapper span {
        white-space: nowrap;
        padding-top: 0.0417rem;
    }
    .dropdownWrapper img {
        width: 0.2188rem;
        height: 0.2188rem;
    }
</style>






