<template>
    <div class="loan">
        <div class="loan__wrapper">
            <div class="loan__type__title">贷款类型</div>
             <Select v-model="model1" style="width: 1.5104rem">
                <Option v-for="item in types" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
        <div class="loan__wrapper">
            <div class="loan__type__title">存款金额</div>
             <Select v-model="model2" style="width: 1.5104rem">
                <Option v-for="item in yans" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
        <div class="loan__wrapper">
            <div class="loan__type__title">还款周期</div>
             <Select v-model="model3" style="width: 1.5104rem">
                <Option v-for="item in month" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
        <div class="loan__wrapper">
            <div class="loan__type__title">存款存入时间</div>
            <DatePicker type="date" placeholder="请选择时间" style="width: 1.5104rem"></DatePicker>
        </div>
        <div class="loan__wrapper">
            <div class="loan__type__title">贷款期限(月)</div>
            <input type="text">
        </div>
        <div class="loan__wrapper">
            <div class="loan__type__title">存款利率(%)</div>
            <input type="text">
        </div>
        <div class="loan__wrapper">
            <div class="loan__type__title">贷款金额</div>
            <input type="text">
        </div>
            
    </div>
</template>
<script>
export default {
   
    data() {
        return {
           types: [
                {
                    value: '普通住房商业贷款',
                    label: '普通住房商业贷款'
                },
                {
                    value: '商业贷款',
                    label: '商业贷款'
                },
              
            ],
            yans: [
                {
                    value: '1500元以下',
                    label: '1500元以下'
                },
                {
                    value: '3500元以下',
                    label: '3500元以下'
                },
            ],
            month: [
                {
                    value: '1月',
                    label: '1月'
                },
                {
                    value: '2月',
                    label: '2月'
                },
                {
                    value: '3月',
                    label: '3月'
                },
                {
                    value: '4月',
                    label: '4月'
                },
                {
                    value: '5月',
                    label: '5月'
                },
                {
                    value: '6月',
                    label: '6月'
                },
                {
                    value: '7月',
                    label: '7月'
                },
                {
                    value: '8月',
                    label: '8月'
                },
                {
                    value: '9月',
                    label: '9月'
                },
                {
                    value: '10月',
                    label: '10月'
                },
                {
                    value: '11月',
                    label: '11月'
                },
                {
                    value: '12月',
                    label: '12月'
                }
            ],
            model1: '',
            model2: '',
            model3: '',
        }
    },
}
</script>
<style scoped>
    .loan {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 0.2604rem;
    }
    .loan__wrapper {
        flex: 0 0 47%;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0.1771rem;
    }
    .loan__type__title {
        font-size: 0.1rem;
        color: #DFEFFF;
    }
    .loan__wrapper input {
        width: 1.5104rem;
        height: 0.1875rem;
        outline: 0;
        font-size: 0.0729rem;
        border: 0;
        color: #DFEFFF;
        background: url('../assets/calculator/Rectangle19Copy@2x.png') no-repeat;
        background-size: contain;
        box-sizing: border-box;
        padding: 0 .05rem;
    }
</style>




