<template>
    <div class="cirque">
        <div class="cirque__item1"></div>
        <div class="cirque__item2"></div>
        <div class="cirque__content">
            <img :src="this.shop.picPath" class="cirque__content__icon">
            <div class="cirque__content__title">{{this.shop.title}}</div>
        </div>
    </div>
</template>
<script>
export default {
   
    props: ['shop'],

}
</script>
<style scoped>
    .cirque {
        position: absolute;
        left: 3.3542rem;
        top: 0.7292rem;
        width: 3.1094rem;
        height: 3.5104rem;
        background: url('../assets/index/_20180823122518.png') no-repeat;
        background-size: contain;
    }
    .cirque__item1 {
        animation: rotate 20s infinite linear;
        position: absolute;
        left: 0.2708rem;
        top: 0.3438rem;
        width: 2.9792rem;
        height: 2.901rem;
        background: url('../assets/index/middle_big_bg3@2x.png') no-repeat;
        background-size: contain;
    }
    .cirque__item2 {
        animation: rotate-re 15s infinite linear;
        position: absolute;
        left: 0.4896rem;
        top: 0.55rem;
        width: 2.5365rem;
        height: 2.5156rem;
        background: url('../assets/index/middle_big_bg1@2x.png') no-repeat;
        background-size: contain;
    }
    .cirque__item3 {
        position: absolute;
        left: 0.2188rem;
        top: 0.1563rem;
        width: 2.5365rem;
        height: 2.5156rem;
        background: url('../assets/index/middle_big_bg2@2x.png') no-repeat;
        background-size: contain;
    }
    .cirque__content {
        width: 3rem;
        height: 3rem;
        position: absolute;
        left: .2635rem;
        top: .3rem;
    }
    .cirque__content__icon {
        position: absolute;
        left: 50%;
        top: 1rem;
        transform: translate(-50%);
        width: 0.5938rem;
        height: 0.5938rem;
    }
    .cirque__content__title {
        position: absolute;
        top: 1.7rem;
        left: 50%;
        transform: translate(-50%);
        color: #6CE6FF;
        font-size: 0.1667rem;
    }
</style>



