import Vue from 'vue'
import Router from 'vue-router'
import App from '../App'

Vue.use(Router)

const routes = [
  {
    path: '/',
    redirect: '/index',
  },
  {
    path: '/index',
    name: 'index',
    meta:{index:1},
    component: App.components['index'],
    
  },
  {
    path: '/shopping',
    name: 'shopping',
    meta:{index:2},
    component: App.components['shopping'],
    
  },
  {
    path: '/market',
    name: 'market',
    meta:{index:2},
    component: App.components['market'],
    children: [
      // {
      //   path: '/market-financing-list.vue',
      //   name: 'market-financing-list.vue',
      //   meta: {index: 3},
      //   component: App.components['market-financing-list']
      // }
    ]
    
  },
  //网点基本信息
  {
    path: '/message',
    name: 'message',
    meta:{index:2},
    component: App.components['message'],
    
  },
  {
    path: '/homeland',
    name: 'homeland',
    meta: {index:2},
    component: App.components['homeland']
  }
]

export default new Router({
  routes,
})



