<template>
    <transition name="fade">
        <div class="shopping">
            <h-header :title="Loption[nowIdx].name"></h-header>
            <ul class="shopping__choice">
                <li class="choice__option" v-for="(item, i) in Loption" :key="i" @click="saveIdx(i)"
                :style="{ backgroundImage: `url(${nowIdx === i ? item.nameurl : item.namegoldurl})`}"></li>
            </ul>

            <div class="shopping__content" v-show="!sdetail">
                <ul class="content__time" v-show="nowIdx === 0">
                    <li class="content__time__rank" v-for="(item, i) in Loption[0].time" :key="i" @click="cgtimebg(i)"
                    :class="{active: curIdx === i}">
                        {{ item }}
                    </li>
                </ul>

                <div class="content__show swiper-container" :class="{contshow: nowIdx !== 0}" style="z-index: 0">
                    <!-- <div class="content__direc-left swiper-button-pre"></div> 
                    <ul class="content__show__ct">
                        <li class="content__show__list swiper-slide" v-for="(item, i) in Loption[nowIdx].list" :key="i" @click="showDetail(i)">
                            <img class="show__list__listurl" :src="item.listurl">
                            <div class="show__list__listnm">{{ item.listnm }}</div>
                            <div class="show__list__cost"> {{ item.cost }} </div>
                            <div class="show__list__current"> {{ item.current }} </div>
                        </li>
                    </ul>
                    <div class="content__direc-right swiper-button-next"></div> -->

                    <swiper :options="swiperOption" ref="mySwiper">
                        <!-- slides -->
                        <swiper-slide v-for="(item, i) in Loption[nowIdx].list" :key="i">
                            <div class="content__show__list" @click="showDetail(i)">
                                <img class="show__list__listurl" :src="item.listurl">
                                <div class="show__list__listnm">{{ item.listnm }}</div>
                                <div class="show__list__cost"> {{ item.cost }} </div>
                                <div class="show__list__current"> {{ item.current }} </div>
                            </div>
                        </swiper-slide>
                        <!-- Optional controls -->
                        <div class="swiper-button-prev content__direc-left" slot="button-prev"></div>
                        <div class="swiper-button-next content__direc-right" slot="button-next"></div>
                    </swiper>
                </div>
            </div>


            <!-- 详情 -->
            <transition name="fade">
                <div class="content__detailct" v-show="sdetail">
                    <div class="content__detail">
                        <div class="content__detail__pictures">
                            <ul class="content__detail__listct">
                                <li class="content__detail__list" v-for="(item, i) in Loption[nowIdx].list[contIdx].introimg" :key="i">
                                    <img class="detail__list__introimg" :src="item">
                                </li>
                            </ul>  
                            <img class="content__detail__large" :src="Loption[nowIdx].list[contIdx].listurl">              
                        </div>
                        <div class="content__detail__line"></div>
                        <ul class="content__detail__message">
                            <li class="detail__message__title"> {{ Loption[nowIdx].list[contIdx].listnm }} </li>
                            <li class="detail__message__current"> {{ Loption[nowIdx].list[contIdx].current }}</li>
                            <li class="detail__message__cost"> {{ Loption[nowIdx].list[contIdx].cost }} </li>
                            <li class="detail__message__code"><img :src="Loption[nowIdx].list[contIdx].codeimg"></li>
                            <li c
                            lass="detail__message__intro">请扫描二维码进行购买</li>
                        </ul>
                        <div class="content__detail__close" @click="close()"><img src="../assets/shopping/close.png"></div>
                    </div>            
                </div>
            </transition>
        </div>        
    </transition>
</template>
<script>
import header from './h-header.vue'
import { mapGetters } from 'vuex'


export default {
    components: {
        'h-header': header
    },
    data() {
        return {
            nowIdx: 0,  //左边导航条对应的json数据
            curIdx: 0,
            contIdx: 0,
            // isActive: false,
            sdetail: false,
            swiperOption: {
                // 所有的参数同 swiper 官方 api 参数
                slidesPerView: 3,
                // spaceBetween: 30,
                slidesPerGroup: 3,
                loop: true,
                loopFillGroupWithBlank: true,
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev'
                }
            },
            // 显示抢购循环的对象
            timeobj: {}
        }
    },
    computed: {
        ...mapGetters([
            'Loption'
        ]),
        swiper() {
            return this.$refs.mySwiper.swiper;
        }
    },
    methods: {
        // 页面加载后根据时段展示相关数据
        timeList() {
            // Json 未定义,待取得数据后修改
            let ten = new Date().setHours(10, 0, 0, 0),
                twelve = new Date().setHours(12, 0, 0, 0),
                fourteen = new Date().setHours(14, 0, 0, 0),
                sixteen = new Date().setHours(16, 0, 0, 0),
                now = new Date();
            if(now > ten && now < twelve) {
                this.timeobj = Json1;
            } else if(now > twelve && now < fourteen) {
                this.timeobj = Json2;
            } else if(now > fourteen && now < sixteen) {
                this.timeobj = Json3;
            } else if (now < sixteen) {
                this.timeobj = Json4;
            }
        },
        saveIdx(idx) {
            this.nowIdx = idx;
            // console.log(idx);
        },
        cgtimebg(idx) {
            // this.isActive = !this.isActive;
            this.curIdx = idx;
        },
        showDetail(idx) {
            this.contIdx = idx;
            this.sdetail = !this.sdetail;
        },
        close() {
            this.sdetail = !this.sdetail;
        }
    },
    mounted() {
        // console.log('this is current swiper instance object', this.swiper)
        this.swiper.slideTo(3, 1000, false);

        // this.timeList();
    }
}
</script>
<style scoped>
    /* 淡入淡出 */
    .trans_flag {
        opacity: 0;
        transition: all .3s;
    }
    .fade_flag {
        opacity: 1!important;
    }

    .shopping {
        font-family: 'PingFangSC-Medium';
        position: relative;
        display: flex;
        justify-content: space-between;
        width: 10rem;
        height: 5.625rem;
        box-sizing: border-box;
        padding-left: 0.4688rem;
        padding-right: 0.4688rem;
    }
    .shopping__header {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0.5052rem; 
        background: url('../assets/shopping/Group13@2x.png') no-repeat;
        background-size: contain;

        font-size: 0.2083rem;
        font-weight: 500;
        color: rgba(223,239,255,1);
        line-height: 0.5052rem;
        text-shadow: 0 0rem 0.0469rem rgba(150,189,255,1);
        text-align: center;
    }
    .shopping__choice {
        margin-top: 1.5104rem;
    }
    .choice__option {
        width: 1.0677rem;
        height: 0.4271rem;
        background-repeat: no-repeat;
        background-size: contain;
        margin-bottom: 0.2708rem;
        /* margin: 290px auto 52px 90px; */
    }
    .shopping__content {
        width: 7.3646rem;
        height: 3.6875rem;
        /* width: 1582px;
        height: 876px; */
        background-image: url('../assets/shopping/二级页面框@2x.png');
        background-repeat: no-repeat;
        background-size: contain;

        margin-top: 0.8958rem;
    }
    .content__time {
        margin-top: 0.474rem;
        margin-left: 0.4896rem;
    }
    .content__time__rank {
        width: 1.1458rem;
        /* height: 0.3646rem; */
        display: inline-block;
        margin-right: 0.1094rem;
        text-align: center;
        line-height: 0.3646rem;
        font-family: 'DINPro-Bold';
        font-size: 0.099rem;
        color: rgba(223,239,255,1);
        background-image: url('../assets/shopping/time.png');
        background-repeat: no-repeat;
        background-size: contain;
    }
    .active {
        background-image: url('../assets/shopping/goldtime.png');
    }
    .content__show {
        position: relative;
        max-width: 6.1875rem;
        /* width: 5.3958rem; */
        height: 2.1094rem;
        margin-top: 0.2656rem;
        margin-left: 0.6302rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        overflow: hidden;
    }
    .contshow {
        margin-top: 0.8125rem;
    }
    .content__direc-left {
        width: 0.1458rem;
        height: 0.3125rem;
        margin-right: 0.2344rem;
        background: url('../assets/shopping/left.png') no-repeat;
        background-size: contain;
    }
    .content__direc-right {
        width: 0.1458rem;
        height: 0.3125rem;
        margin-left: 0.2344rem;
        background: url('../assets/shopping/right.png') no-repeat;
        background-size: contain;
    }
    .content__show__ct {
        max-width: 5.3958rem;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        overflow: hidden;
    }
    .content__show__list {
        box-sizing: border-box;
        flex-shrink: 0;
        width: 1.4479rem;
        /* width: 1.4896rem; */
        height: 2.1094rem;
        padding-top: 0.1927rem;
        /* margin-right: 0.526rem; */
        margin: 0 auto;
        background: url('../assets/shopping/Group7@2x.png') no-repeat;
        background-size: contain;
        color: rgba(223,239,255,1);
        font-size: 0.1042rem; 
        line-height: 0.1458rem;
        text-align: center;
    }
    .show__list__listurl {
        width: 1.1667rem;
        height: 0.9792rem;
        margin-bottom: 0.0521rem;
    }
    .show__list__listnm {
        margin-bottom: 0.1875rem;
    }
    .show__list__cost {
        text-decoration: line-through;
        margin-bottom: 0.0573rem;
    }
    .show__list__current {
        font-size:0.125rem;
        color: rgba(255,51,51,1);
        line-height: 0.1719rem;
    }


    /* 详情 */
    .content__detailct {
        position: absolute;
        z-index: 12;
        top: 0;
        right: 0;
        width: 1920px;
        height: 1080px;
        background: rgba(16,16,16,.3);
    }
    .content__detail {
        position: absolute;
        left: 397px;
        display: flex;
        /* justify-content: space-between; */
        width: 7.5208rem;
        height: 3.7292rem;
        margin-top: 0.8906rem;
        background: url('../assets/shopping/detailbg.png') no-repeat;
        background-size: contain;
    }
    .content__detail__pictures {
        display: flex;
        justify-content: space-between;
        box-sizing: border-box;
        width: 4.5rem;
        height: 2.4896rem;
        margin-top: 0.599rem;
        margin-left: 0.5573rem;
        padding-top: 0.2604rem;
        padding-left: 0.1875rem; 
        padding-right: 0.2188rem;
        background: url('../assets/shopping/detailctbg.png') no-repeat;
        background-size: contain;
    }
    .content__detail__listct {
        display: flex;
        flex-direction: column;
        overflow: hidden;
        max-height: 2.1042rem;
    }
    .content__detail__list {
        margin-bottom: 0.099rem;
    }
    /* 设计尺寸第一张图较大，第二张图开始 透明度递减 */
    .detail__list__introimg:first-child {
        width: 1.5729rem;
        height: 0.5104rem;
    } 
    .detail__list__introimg {
        width: 1.4792rem;
        height: 0.4896rem;
    }

    .content__detail__large {
        width: 2.125rem;
        height: 1.9583rem;
    }

    .content__detail__line {
        width: 0.0156rem;
        height: 2.6198rem;
        margin-top: 0.6042rem;
        margin-left: 0.2969rem;
        background: url('../assets/shopping/detailline.png') no-repeat;
        background-size: contain;
    }
    .content__detail__message {
        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        align-items: center;
        margin-top: 0.8594rem;
        margin-left: 0.1719rem;
        color:rgba(223,239,255,1);
    }
    .detail__message__title {
        font-size: 0.1563rem;
        line-height: 0.2188rem;
    }
    .detail__message__current {
        margin-top: 0.1563rem;
        font-size: 0.1458rem;
        color:rgba(255,51,51,1);
        line-height: 0.2083rem;
    }
    .detail__message__cost {
        font-size: 0.125rem;
        line-height: 0.1719rem;
        text-decoration: line-through;
    }
    .detail__message__code {
        margin-top: 0.2708rem;
        width: 0.7813rem;
        height: 0.7813rem;
    }
    .detail__message__code img {
        width: 100%;
    }
    .detail__message__intro {
        margin-top: 0.0521rem;
    }
    .content__detail__close img {
        width: 0.401rem;
        height: 0.401rem;
        margin-top: 0.0781rem;
        transform: translateX(-0.0156rem);
    }
</style>





