<template>
    <div class="repay">
        <div class="repay__wrapper">
            <div class="repay__type__title">贷款类型</div>
             <Select v-model="model1" style="width: 1.5104rem">
                <Option v-for="item in types" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
        <div class="repay__wrapper">
            <div class="repay__type__title">还款方式</div>
             <Select v-model="model2" style="width: 1.5104rem">
                <Option v-for="item in modes" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
        <div class="repay__wrapper">
            <div class="repay__type__title">还款周期</div>
             <Select v-model="model3" style="width: 1.5104rem">
                <Option v-for="item in month" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
        <div class="repay__wrapper">
            <div class="repay__type__title">存款存入时间</div>
            <DatePicker type="date" placeholder="请选择时间" style="width: 1.5104rem"></DatePicker>
        </div>
        <div class="repay__wrapper">
            <div class="repay__type__title">贷款期限(月)</div>
            <input type="text" placeholder="">
        </div>
        <div class="repay__wrapper">
            <div class="repay__type__title">存款利率(%)</div>
            <input type="text">
        </div>
        <div class="repay__wrapper">
            <div class="repay__type__title">贷款金额</div>
            <input type="text">
        </div>
         <div class="repay__wrapper">
            <div class="repay__type__title">提前还款日期</div>
            <DatePicker type="date" placeholder="请选择时间" style="width: 1.5104rem"></DatePicker>
        </div>
        <div class="repay__wrapper">
            <div class="repay__type__title">提前还款方式</div>
             <Select v-model="model4" style="width: 1.5104rem">
                <Option v-for="item in remodes" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
            
    </div>
</template>
<script>
export default {
   
    data() {
        return {
           types: [
                {
                    value: '普通住房商业贷款',
                    label: '普通住房商业贷款'
                },
                {
                    value: '商业贷款',
                    label: '商业贷款'
                },
              
            ],
            modes: [
                {
                    value: '等额本息',
                    label: '等额本息'
                },
            ],
            yans: [
                {
                    value: '1500元以下',
                    label: '1500元以下'
                },
                {
                    value: '3500元以下',
                    label: '3500元以下'
                },
            ],
            month: [
                {
                    value: '1月',
                    label: '1月'
                },
                {
                    value: '2月',
                    label: '2月'
                },
              
            ],
            remodes: [
                {
                    value: '一次全部还清',
                    label: '一次全部还清'
                },
            ],
            model1: '',
            model2: '',
            model3: '',
            model4: '',
        }
    },
}
</script>
<style scoped>
    .repay {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 0.2604rem;
    }
    .repay__wrapper {
        flex: 0 0 47%;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0.1771rem;
    }
    .repay__type__title {
        font-size: 0.1rem;
        color: #DFEFFF;
    }
    .repay__wrapper input {
        width: 1.5104rem;
        height: 0.1875rem;
        outline: 0;
        font-size: 0.0729rem;
        border: 0;
        color: #DFEFFF;
        background: url('../assets/calculator/Rectangle19Copy@2x.png') no-repeat;
        background-size: contain;
        box-sizing: border-box;
        padding: 0 .05rem;
    }
</style>




