<template>
    <div class="fund">
        <div class="fund--index" v-show="!showList">
            <fund-item class="fund__item" v-for="(fund, key) in funds" :title="fund" :key="key" @click.native="openList(key)"></fund-item>
            <div class="fund--bg"></div>
            <!-- 换一批 -->
            <div class="fund__toggle">换一批</div>
        </div>
        <fund-list v-show="showList"></fund-list>
    </div>
</template>
<script>
import fundItem from './market-fund-item.vue'
import fundList from './market-fund-list.vue'

// import {bilibili} from '@/api/api.js'

export default {
    created() {
        
    },
    components: {
        'fund-item': fundItem,
        'fund-list': fundList
    },
    data() {
        return {
            funds: ['混合型', '其他', '债卷型', 'FOF型', '货币市场型','股票型', 'QDII型'],
            showList: false
        }
    },
    methods: {
        openList(index) {
            this.showList = true;
            console.log(index)
        }
    }
}
</script>
<style scoped>
    .fund--bg {
        position: absolute;
        width: 6.901rem;
        height: 2.901rem;
        left: 2.2188rem;
        top: 1.1771rem;
        background: url('../assets/market/fund/Group12@2x.png') no-repeat;
        background-size: contain;
    }
    .fund__item:nth-of-type(1) {
        position: absolute;
        width: 1.1615rem;
        height: 1.1615rem;
        left: 5.6771rem;
        top: 2.0625rem;
        font-size: 0.1667rem;
    }
    .fund__item:nth-of-type(2) {
        position: absolute;
        width: 0.5677rem;
        height: 0.5677rem;
        left: 2.1094rem;
        top: 2.3073rem;
        font-size: 0.1042rem;
    }
    .fund__item:nth-of-type(3) {
        position: absolute;
        width: 0.8646rem;
        height: 0.8646rem;
        left: 3.3646rem;
        top: 1.5052rem;
        font-size: 0.1354rem;
    }
    .fund__item:nth-of-type(4) {
        position: absolute;
        width: 0.75rem;
        height: 0.75rem;
        left: 4.1458rem;
        top: 3.5677rem;
        font-size: 0.1146rem;
    }
    .fund__item:nth-of-type(5) {
        position: absolute;
        width: 0.75rem;
        height: 0.75rem;
        top: 0.7448rem;
        left: 6.8385rem;
        font-size: 0.0938rem;
    }
    .fund__item:nth-of-type(6) {
        position: absolute;
        width: 1.1198rem;
        height: 1.1198rem;
        left: 7.901rem;
        top: 2.7031rem;
        font-size: 0.1667rem;
    }
    .fund__item:nth-of-type(7) {
        position: absolute;
        width: 0.651rem;
        height: 0.651rem;
        top: 1.25rem;
        left: 8.9479rem;
        font-size: 0.1042rem;
    }
    /* 换一批 */
    .fund__toggle {
        position: absolute;
        top: 4.3854rem;
        left: 8.7917rem;
        width: 0.6198rem;
        height: 0.224rem;
        background: url('../assets/market/fund/inside_down_change@2x.png') no-repeat;
        background-size: contain;
        font-size: 0.1042rem;
        line-height: 0.224rem;
        text-align: center;
        color: #DFEFFF;
    }
</style>



