

function bilibili(id) {
    // let foo = 0;
    return new Promise(function (resolve) {
        setTimeout(function () {
            // foo =101010;
            resolve(id)
        })
    });
}


/**
 * 获取机构号 返回一个Promise
 */
function getCurrentInsId(){
    return new Promise(function (resolve) {
        boundAsync.getCurrentInsID().then(function(InsID){
            if (InsID == ""){
                console.log("未获取到机构号");
            }
            resolve(InsID);
        });
    });
}

//封装P2接口
function callP2Service(simpleTransaction, transId, jsonData){
	const jsonDataQry = {
        _fw_service_id: simpleTransaction,
        transaction_id: transId,
        jsonData: JSON.stringify(jsonData)
    };
    return new Promise(function (resolve, reject) {
        boundAsync.p2TransRequest(JSON.stringify(jsonDataQry)).then(function (rs) {
            try{
                // rs = JSON.parse(rs);
                resolve(rs);
            }catch(e){
                console.log("数据错误，返回数据为：")
            }        
        });
    }); 
}


/**
 * 获取理财列表
 */
function getFinancial() {
    return new Promise(function (resolve, reject) {
        //获取机构号
        getCurrentInsId().then((InsID) => {
            const JsonData = {
                _sql_id: "prct.getAll",
                province_id: InsID.substring(0,3),
                orgCode: InsID,
                BRANCH_ID: InsID,
                _start: 0,
                _max_rows: 6
            };
            callP2Service('databaseTransaction', 'findPaginatedList', JsonData)
                .then((rs) => resolve(rs))
                .catch((err) => reject(err));
        }).catch((err) => reject(err));
    });
}

/**
 * 获取基金产品列表
 */
function getFundList(){
    return new Promise(function (resolve, reject) {
        //获取机构号
        getCurrentInsId().then((InsID) => {
            const JsonData = {
                _sql_id: 'fund.fundListNew',
                orgCode: InsID,
                BRANCH_ID: InsID,//网点号
                TXN_ITT_CHNL_CGY_CODE: "20190031",
                _start: 0 * 10,
                _max_rows: 10,
            };
            callP2Service("databaseTransaction","findPaginatedList", JsonData)
                .then((rs) => resolve(rs))
                .catch((err) => reject(err));
        }).catch((err) => reject(err));
    });
}

/**
 * 获取账户贵金属列表单位
 */
function getNobleMetalUnit() {
    return new Promise(function (resolve, reject) {
        //获取机构号
        getCurrentInsId().then((InsID) => {
            const JsonData = {
                TXN_ITT_CHNL_CGY_CODE: '20190031',
                BRANCH_ID : InsID, //机构号
                OPER_CODE : "zhyhhdzd",
            };
            callP2Service('simpleTransaction', 'A0301G306', JsonData)
                .then((rs) => resolve(rs))
                .catch((err) => reject(err));
        }).catch((err) => reject(err)); 
    });
}

/**
 * 获取贵金属列表数据
 */
function loadMetalList(){
    return new Promise(function (resolve, reject) {
        //获取机构号
        getCurrentInsId().then((InsID) => {
            //查询价格
            const params = {
                PM_Txn_Vrty_List: [
                    {PM_Txn_Vrty_Cd: '01'},
                    {PM_Txn_Vrty_Cd: '15'},
                    {PM_Txn_Vrty_Cd: '16'},
                    {PM_Txn_Vrty_Cd: '17'},
                    {PM_Txn_Vrty_Cd: '18'},
                    {PM_Txn_Vrty_Cd: '19'},
                    {PM_Txn_Vrty_Cd: '20'},
                    {PM_Txn_Vrty_Cd: '21'},
                    {PM_Txn_Vrty_Cd: '22'},
                    {PM_Txn_Vrty_Cd: '02'}
                ],
                BRANCH_ID : InsID,
                OPER_CODE : "zhyhhdzd",
                TXN_ITT_CHNL_CGY_CODE: '20190031',
            };
            callP2Service("simpleTransaction", "A1142P007-pad", params)
                .then((resp) => resolve(resp))
                .catch((err) => reject(err));
        }).catch((err) => reject(err));
    });
}

/**
 * 龙智投 按风险 、投资期限查询
 */
function longzt() {
    return new Promise(function (resolve, reject) {
        //获取机构号
        getCurrentInsId().then((InsID) => {
            const JsonData = {
                "OPER_CODE": "zhyhhdzd",
                "BRANCH_ID": InsID, //机构号
                "Frcst_Ivs_Ddln_TpCd": "03", //投资期限
                "Cst_Rsk_Tlrnc_Cpy_Cd": "06"//风险承受	
            };
            callP2Service('simpleTransaction', 'A0591U101-zntg-stm', JsonData)
                .then((rs) => resolve(rs))
                .catch((err) => reject(err));
        }).catch((err) => reject(err));;  
    });
}

/**
 * 某机构月均准点率
 */
function getHistoryRate() {
    return new Promise(function (resolve, reject) {
        //获取机构号
        getCurrentInsId().then((InsID) => {
            const JsonData = {
                "TABLE_NM": "prod:EcphbIndexFinalDay",
                "ROW_KEY": InsID + "|2002002|2014004|2012004|2003006|2004001|99999999|99999999|99999999|99999999|99999999|99999999",
                "TABLE_FD": [{
                        "COL_NM": "col20",
                        "COL_TP": "String"
                    },
                    {
                        "COL_NM": "col3",
                        "COL_TP": "String"
                    },
                    {
                        "COL_NM": "col4",
                        "COL_TP": "String"
                    },
                    {
                        "COL_NM": "col1",
                        "COL_TP": "String"
                    }
                ],
                "Rmrk": "",
                "OPER_CODE": "zhyhhdzd"
            };
            callP2Service('simpleTransaction', 'AP201I002_ici', JsonData)
                .then((rs) => resolve(rs))
                .catch((err) => reject(err));
        }).catch((err) => reject(err));
    });
}

