/**
 * 查询精选房源 品牌信息
 * @param {用户输入数据} params_bespeak
 */
function bespeakHouse(params_bespeak) {
    return new Promise(function (resolve, reject) {
        getCurrentInsId().then((InsID) => {
            const JsonData = {
                "Urbn_Cd": params_bespeak.Urbn_Cd, //城市代码
                "Usr_Nm": params_bespeak.Usr_Nm, //用户姓名
                "MblPh_No": params_bespeak.MblPh_No, //手机号码
                "Idv_Crdt_Tp_List_Inf": params_bespeak.Idv_Crdt_Tp_List_Inf, //个人证件类型列表信息
                "Orig_Crdt_No": params_bespeak.Orig_Crdt_No, //原始证件号码
                "Prj_ECD_Inf": params_bespeak.Prj_ECD_Inf, //项目编码信息
                "New_Hs_Nm": params_bespeak.New_Hs_Nm, //新房屋名称
                //"Rsrvtn_Tm": params_bespeak.Rsrvtn_Tm.replace(/-/g,"").replace(/:/g,"").replace(/\s+/g,""), //预约时间
                "Rsrvtn_Tm": params_bespeak.Rsrvtn_Tm.split(" ")[0].replace(/-/g,"")+"100000", //预约时间 默认上午十点预约
                //"chckin_Dt": params_bespeak.chckin_Dt.replace(/-/g,"").replace(/:/g,"").replace(/\s+/g,""), //入住日期
                "Apd_Amt_Dsc": params_bespeak.Apd_Amt_Dsc, //附加金额描述
                "Dt_Cmnt": params_bespeak.Dt_Cmnt, //入住说明
                "CCBIns_ID": InsID, //建行机构编号
                "BRANCH_ID": InsID, //建行机构编号
                "OPER_CODE": "zhyhhdzd" //操作员号
            };
            callP2Service('simpleTransaction', 'P5201CL10-pad', JsonData)
                .then((rs) => resolve(rs))
                .catch((err) => reject(err));
        }).catch((err) => reject(err));;  
    });
}



