<template>
<transition name="fade">
    <div class="wrapper" v-show="isShow">
        <div class="call">
            <div class="call__header">
                <div class="call__header__title">{{ this.title }}</div>
                <div class="call__header__light"></div>
                <div class="call__header__close" @click="isShow=false"></div>
            </div>
            <div class="call__list">
                <div class="call__list__item" v-for="(window, key) in windows" :key="key">
                    <div class="call__list__item__header"></div>
                    <div class="call__list__item--win">{{ window.win }}</div>
                    <div class="call__list__item--desc">{{ window.desc }}</div>
                    <div class="call__list__item--time">{{ window.time }}</div>
                </div> 
               
            </div>
        </div>
    </div>
</transition>
</template>
<script>
export default {
    props: {
        value: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            title: '已呼叫列表',
            isShow: this.value,
            windows: [
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
                {win: 'D024', desc: '请到2号窗口', time: 12101},
            ]
        }     
    },
    methods: {
        
    },
    watch: {
        value: function (val) {
            this.isShow = val;
        },
        isShow: function (val) {
            this.$emit('input', val);
        }
    }
}
</script>
<style scoped>
    /* 返回按钮过渡动画 */
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
    .wrapper {
        position: absolute;
        left: 0;
        top: 0;
        width: 10rem;
        height: 5.625rem;
        background-color: rgba(0, 0, 0, .5);
    }
    .call {
        position: absolute;
        padding-top: 0.8073rem;
        box-sizing: border-box;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 5.4531rem;
        height: 3.8854rem;
        background: url('../assets/call/Rectangle6@2x.png') no-repeat;
        background-size: contain;
    }
    .call__header {
        position: absolute;
        top: 0;
        width: 5.4531rem;
        height: 0.4896rem;
        background: url('../assets/call/Group36@2x.png') no-repeat;
        background-size: 100% 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .call__header__title {
        color: #fff;
        font-size: 0.1458rem;
    }
    .call__header__light {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 1.3333rem;
        height: 0.2396rem;
        background: url('../assets/call/Oval11@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .call__header__close {
        position: absolute;
        right: 0.0885rem;
        top: 0.1771rem;
        width: 0.1354rem;
        height: 0.1354rem;
        background: url('../assets/common/Group8@2x.png') no-repeat;
        background-size: contain;
    }
    /* 呼号窗口 */
    .call__list {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        color: #DFEFFF;
        font-size: 0.0833rem;
    }
    .call__list__item {
        position: relative;
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 2.1458rem;
        height: 0.4271rem;
        background: url('../assets/call/Rectangle8@2x.png') no-repeat;
        background-size: 100% 100%;
        margin-bottom: 0.1563rem;
    }
    .call__list__item__header {
        position: absolute;
        top: 0;
        left: 0;
        width: 0.724rem;
        height: 0.0469rem;
        background: url('../assets/call/Rectangle81@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .call__list__item--win {
        font-size: 0.099rem;
    }
</style>



