<template>
    <transition name="fade">
        <div class="trans">
            <div class="trans__main">
                <trans-list></trans-list>
            </div>
        </div>
    </transition>    
</template>
<script>
import { mapGetters } from 'vuex'
import transList from './market-transfer-list.vue'


export default {
    components: {
        transList
       
    },
    computed: {
        ...mapGetters([
            'Market'
        ])
    },
    data() {
        return {

        }
    },
    methods: {
        closeSenior() {
            this.showSenior = !this.showSenior;
        },
        closeDetail() {
            this.details = !this.details;
        }
    }
}
</script>
<style scoped>
    .trans {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        font-family: 'PingFangSC-Medium';
        font-weight: 600;
        color: rgba(223,239,255,1);
    }
    .trans__main {
        position: relative;
    }
</style>



