<script>
import header from './h-header.vue'
import search from './search.vue'
import { mapGetters } from 'vuex'

export default {
    components: {
        'h-header': header,
        search
    },
    computed: {
        //对对象解构
        ...mapGetters([
            'Lhomeland',
            'LhomelandDetail'
        ])
    },
    data() {
        return {
            title: '建融家园',
            nowIdx: 0,
            seniorIdx: -1,
            areaIdx: -1,
            more: false,
            senior: false,
            detail: false,
            order: false,
            fadeFlag: true, // 控制渐变
            hTime: '',
            cityList: [
                {
                    value: '大陆身份证',
                    label: '大陆身份证'
                },
                {
                    value: '军官证',
                    label: '军官证'
                }
            ],
            // model1: '大陆身份证',
            budgetList: [
                {
                    value: '1500元以下',
                    label: '1500元以下'
                },
                {
                    value: '1500元以上',
                    label: '1500元以上'
                }
            ],
            checkinList: [
                {
                    value: '立即入住',
                    label: '立即入住'
                },
                {
                    value: '预约入住',
                    label: '预约入住'
                }
            ],
            valueSlide: [1, 10],
            newObj: [],
            forIdx: 0
        }
    },
    methods: {
        mainIntro(idx) {
            this.nowIdx = idx;
        },
        showMore() {
            this.more = !this.more;
        },
        showSenior() {
            this.senior = !this.senior;
        },
        closeSenior() {
            this.senior = !this.senior;
        },
        seniorbg(idx) {
            this.seniorIdx = idx;
        },
        areabg(idx) {
            this.areaIdx = idx;
        },
        showDetail() {
            this.detail = !this.detail;
            // this.more = !this.more;
        },
        orderHouse() {
            this.order = !this.order;
        },
        replaceimg() {
            let homeland = this.Lhomeland;
            this.fadeFlag = false;
            setTimeout(() => {
                if(this.forIdx >= homeland.length - 1) {
                    this.forIdx = 0;
                    this.newObj = homeland.slice(this.forIdx, this.forIdx + 4);
                } else {
                    this.newObj = homeland.slice(this.forIdx, this.forIdx + 4);
                    this.forIdx += 4;
                }
                this.fadeFlag = true;
            }, 300);
        }
    },
    created() {
        // this.newObj = this.Lhomeland.slice(0, 4);
        this.replaceimg();
    }
}
</script>


