<template>
    <div class="wish__body">
        <textarea cols="30" rows="10" class="wish__body__wall--big" :placeholder="inputBig"></textarea>
        <input type="text" class="wish__body__input--name" :placeholder="inputDesc.name">
        <input type="text" class="wish__body__input--call" :placeholder="inputDesc.call">
        
        <div class="wish__body__wall--reset">重置</div>
        <div class="wish__body__wall--submit">提交</div>
    </div>
</template>
<script>
export default {
   
    data() {
        return {
            inputDesc: {
                name: '请输入TA的名字（非必填）',
                call: '请输入TA的手机号（非必填）',
            },
            inputBig: '你可以在这里输入您你想要说的话~',
        }
    },
}
</script>
<style scoped>
    /* 心愿墙 */
    .wish__body__wall--big {
        position: absolute;
        top: 0.8854rem;
        left: 0.4167rem;
        width: 3.7813rem;
        height: 2.5365rem;
        background: url('../assets/wish/Rectangle10Copy@2x.png') no-repeat;
        background-size: 100% 100%;
        resize : none;
        color: #DFEFFF;
        font-size: 0.125rem;
        text-shadow:0px 0px 0.0938rem rgba(136,221,255,1);
        
        padding: .1rem;
        box-sizing: border-box;
        border: 0;
        outline: none;
        background-color: transparent; 
    }
    .wish__body__wall--big::placeholder {
        color: #DFEFFF;
        font-size: 0.125rem;
    }
    /* 输入 */
    .wish__body__input--call,
    .wish__body__input--name {
        box-sizing: border-box;
        font-size: 0.1rem;
        line-height: 0.2083rem;
        padding: 0 .08rem;
        width: 1.875rem;
        height: 0.2083rem;
        background: url('../assets/wish/Rectangle10Copy2@2x.png') no-repeat;
        background-size: contain;
        color: #88DDFF;
        outline: none;
    }
    .wish__body__input--call {
        position: absolute;
        top: 0.91rem;
        right: 0.4323rem;
    }
    .wish__body__input--name {
        position: absolute;
        top: 1.25rem;
        right: 0.4323rem;
    }
    .wish__body__input--name::placeholder,
    .wish__body__input--call::placeholder {
        font-size: 0.0729rem;
        color: #C3D4E7;
        /* line-height: 0.9rem; */
    }
    /* 心愿预览 */
    .wish__body__wall--preview {
        position: absolute;
        top: 1.599rem;
        right: 0.4219rem;
        width: 1.8906rem;
        height: 1.4635rem;
        background: url('../assets/wish/Rectangle10@2x.png') no-repeat;
        background-size: contain;
    }
    .wish__body__wall--preview__title {
        height: 0.2917rem;
        width: 1.8802rem;
        text-align: center;
        font-size: 0.125rem;
        
        line-height: 0.27rem;
        color:rgba(223,239,255,1);
        text-shadow:0px 0rem 0.0938rem rgba(136,221,255,1);
        background: url('../assets/wish/Group39@2x.png') no-repeat;
        background-size: contain;
    }
    .wish__body__wall--preview__txt {
        color: #88DDFF;
        font-size: 0.0938rem;
        text-shadow:0px 0px 0.0938rem rgba(136,221,255,1);
        width: 100%;
        height: 1.1719rem;
        padding: .1rem;
        box-sizing: border-box;
        border: 0;
        outline: none;
        background-color: transparent; 
    }
    .wish__body__wall--reset {
        position: absolute;  
        left: 4.5052rem;
        top: 3.2135rem;
        width: 0.7031rem;
        height: 0.2135rem;
        background: url('../assets/wish/Group20@2x.png') no-repeat;
        background-size: contain;
        color: #C3D4E7;
        line-height: 0.2135rem;
        text-align: center;
        font-size: 0.1042rem;
    }
    .wish__body__wall--submit {
        position: absolute;
        left: 5.6719rem;
        top: 3.2135rem;
        width: 0.7031rem;
        height: 0.2135rem;
        background: url('../assets/common/Group201@2x.png') no-repeat;
        background-size: contain;
        color: #C3D4E7;
        line-height: 0.2135rem;
        text-align: center;
        font-size: 0.1042rem;
    }
</style>




