<template>
    <div class="h-header">
        <div class="h-header__title">{{this.title}}</div>      
    </div>
</template>
<script>
export default {
    props: ['title']
}
</script>
<style scoped>
    .h-header{
        position: absolute;
        top: 0;
        left: 0;
        width: 10rem;
        height: 0.5052rem; 
        background: url('../assets/common/Group13@2x.png') no-repeat;
        background-size: contain;
        font-size: 0.2083rem;
        font-weight: 500;
        color: rgba(223,239,255,1);
        line-height: 0.5052rem;
        text-shadow: 0px 0px 9px rgba(150,189,255,1);
        text-align: center;
    }
</style>





