<template>
    <div class="wrapper">
        <div class="bulletin">
            <div class="bulletin__title">{{this.data[0].title}}</div>
            <div class="bulletin__text"><vue-typer :text="this.data[0].text" :repeat='0' :type-delay='30'></vue-typer></div>
            
        </div>
    </div>
    
</template>
<script>
import { VueTyper } from 'vue-typer'

export default {
    props: ['shop'],
    components: {
        'vue-typer': VueTyper
    },
    data() {
        return {
           data: [{
                title: '1',
                text: '1'
           }]
        }
    },
    mounted() {
        setTimeout( () => {
            this.data = this.shop;
        }, 0)
    },
    
    
}
</script>
<style scoped> 
    .wrapper {
        transform: translateY(0.05rem);
        animation: float-mv-btm 4s .5s infinite ease-in-out;
        perspective: 2000px;
        position: absolute;
        top: 0.8854rem;
        left: 0.7396rem;
    }
    .bulletin {
        width: 2.0313rem;
        height: 2.7344rem;
        background: url('../assets/index/left_bg_text@2x.png') no-repeat;
        background-size: contain;
        transform: rotateY(30deg);
    }
    .bulletin__title {
        padding-top: 0.151rem;
        padding-left: 0.1927rem;
        color: #fff;
        font-size: 0.1563rem;
        text-shadow:0px 0px 8px #fff, 0px 0px 42px #96BDFF, 0px 0px 72px #96BDFF,0px 0px 150px #96BDFF;
    }
    .bulletin__text {
        font-size: 0.0833rem;
        line-height: 0.1667rem;
        color: #6CE6FF;
        padding:0 0.1927rem;
        margin-top: 0.2344rem;       
    }
</style>
<style>
    .vue-typer .custom.char.typed {
        color: #6CE6FF ;
    }
</style>




