<template>
    <div class="wrapper">
        <cirque :shop="shop[0]" @click.native="shopping(shop[0].router)"></cirque>
        <cirque-small class="cir-top" :shop="shop[1]" @click.native="shopping(shop[1].router)"></cirque-small>
        <cirque-small class="cir-mid" :shop="shop[2]" @click.native="shopping(shop[2].router)"></cirque-small>
        <cirque-small class="cir-btm" :shop="shop[3]" @click.native="shopping(shop[3].router)"></cirque-small>
    </div>
</template>
<script>
import cirque from './cirqueMain.vue';
import cirqueSmall from './cirqueSmall.vue';

export default {
    components: {
        'cirque': cirque,
        'cirque-small': cirqueSmall
    },
    created() {
        this.sortArray()
    },
    data() {
        return {
            shop: [
                {
                    picPath: require('../assets/index/middle_big_icon@2x.png'),
                    title: '基本网点信息',
                    text: '网点基本信息查询模块主要展示以当前网点为中心，本机构二级行范围内建行网点的基本信息（含网点地址、网点营业时间、网点联系方式、月均准点率），网点未来七天的准点率预测数据，以及网点对外公告内容。',
                    router: '/message'
                },
                {
                    picPath: require('../assets/index/middle_big_icon1@2x.png'),
                    title: '善融商务商城',
                    text: '善融商务个人商城是建设银行旗下B2C购物平台，支持担保支付和分期付款，是建行致力打造的国内创新型电子商务金融服务平台。该模块将商城的商品以善融精选、善融扶贫、限时抢购、分期优选及当地特惠共五大类产品进行展示，好产品，优惠多。',
                    router: '/shopping'
                },
                {
                    picPath: require('../assets/index/middle_big_icon2@2x.png'),
                    title: '建融家园',
                    text: 'CCB建融家园”是建行为推进长租房市场建设和住房供给侧改革，联合招商蛇口、华润置地、中海外、万科、碧桂园、恒大、星河、天安骏业、心海滕楗、鹏润达、盛隆兴业、荣超等房地产公司，共同打造的品牌长租社区。建行长租房社区具有“租期长、品质高、门槛低、轻负担、新共享、新模式”的核心特点。',
                    router: '/homeland'
                },
                {
                    picPath: require('../assets/index/middle_big_icon3@2x.png'),
                    title: '金融超市',
                    text: '金融超市模块主要展示我行理财产品、基金、保险、账户贵金属、信用卡及贷款产品的基本信息，且支持理财产品、基金、保险、账户贵金属可售级产品的手机银行扫码购或加入购物车后在智慧柜员机一键购买；以及信用卡及贷款产品的广告信息展示。',
                    router: '/market'
                }
            ]
        }
    },
    methods: {
        sortArray() {
            let that = this;
            setInterval(function () {
                let item = that.shop.shift();
                that.shop.push(item);
            }, 6000)
        },
        sendData() {
            return this.shop;
        },
        shopping(router) {
            this.$router.push({ path: router });
        },
        
    }
}
</script>

<style scoped>
    /* .wrapper {
        position: absolute;
        width: 100%;
        height: 100%;
    } */
    .cir-top {
        transform: translateY(-0.05rem);
        animation: float-mv 4s infinite ease-in-out;
        position: absolute;
        right: 1.5417rem;
        top: 0.849rem;
    }
    .cir-mid {
        transform: translateY(-0.05rem);
        animation: float-mv 4s 1s infinite ease-in-out;
        position: absolute;
        right: 1.0938rem;
        top: 2.4063rem;
    }
    .cir-btm {
        transform: translateY(0.05rem);
        animation: float-mv-btm 4s infinite ease-in-out;
        position: absolute;
        right: 1.9375rem;
        top: 3.599rem;
    }
</style>




