<template>
    <div class="market">
        <h-header :title="Market[navIdx].name" v-show="giveData.show"></h-header>
        
        <ul class="market__nav" v-show="giveData.show">
            <li class="market__nav__item" v-for="(item, i) in Market" :key="i" @click="curNav(i)"
             :style="{backgroundImage: `url(${navIdx === i ? item.gnavbg : item.navbg})`}"></li>
        </ul>
        <fund v-show="navIdx === 0" :navIdx="navIdx"></fund>
        <metal v-show="navIdx === 1"></metal>
        <transfer v-show="navIdx === 2"></transfer>
        <insur v-show="navIdx === 3" :msg="giveData"></insur>
        <invest v-show="navIdx === 4"></invest>
        <financing v-show="navIdx === 5"></financing>
       
        <!-- <router-view><router-view> -->
    </div>
</template>
<script>
import header from './h-header.vue'
import { mapGetters } from 'vuex'
import fund from './market-fund.vue'
import metal from './market-metal.vue'
import transfer from './market-transfer.vue'
import insur from './market-insurance.vue'
import invest from './market-invest.vue'
import financing from './market-financing.vue'

// import {bilibili} from '@/api/api.js'

export default {
    // created() {
    //     console.log(bilibili())
    // },
    components: {
        'h-header': header,
        fund,
        metal,
        transfer,
        insur,
        invest,
        financing
    },
    computed: {
        ...mapGetters([
            'Market'
        ])
    },
    data() {
        return {
            navIdx: 0,
            giveData: {
                show: true
            }
        }
    },
    methods: {
        curNav(idx) {
            this.navIdx = idx;
        },
        addNav() {
            setInterval( () => {
                this.navIdx++
            }, 5000);
        }      
    },
    mounted() {
        // this.addNav();
    }
}
</script>
<style scoped>
    .market {
        position: relative;
        box-sizing: border-box;
        padding-left: 0.5365rem;
        
    }
    .market__nav {
        position: absolute;
        top: 0.9323rem;
        left: 0.5365rem;
    }
    .market__nav__item {
        width: 1.0677rem;
        height: 0.4323rem;
        margin-bottom: 0.2083rem;
        background-repeat: no-repeat;
        background-size: contain;
    }
</style>





