<template>
<transition name="fade">
    <div class="wrapper" v-show="isShow">
        <div class="card">
            <div class="card__header">
                <div class="card__header__title">{{ this.title }}</div>
                <div class="card__header__light"></div>
                <div class="card__header__close" @click="isShow=false"></div>
            </div>
            <div class="call__list">
                <div class="call__list__item" v-for="(shop, key) in shoping" :key="key">
                    <div class="call__list__item__bar"></div>
                    <div class="call__list__item__img">
                        <img :src="shop.img">
                        <div class="call__list__item__img--bg"></div>
                    </div>
                    <div class="call__list__item__title">{{ shop.title }}</div>
                    <div class="call__list__item__data" v-for="(rate, key) in shop.rate" :key="key">
                        <div class="call__list__item__data--num">{{ rate.num }}</div>
                        <div class="call__list__item__data--desc">{{ rate.desc }}</div>
                    </div>
                    <div class="call__list__item__input">
                        <input type="text" :placeholder="inputDesc">元
                    </div>
                    <div class="call__list__item__data__QR">
                        <img :src="shop.QR" alt="">
                    </div>
                    <div class="call__list__item__data__remove">
                        <i></i>
                    </div>
                </div>
            </div>
            
        </div>
    </div>
</transition>
</template>
<script>
export default {
    props: {
        value: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            title: '购物车',
            isShow: this.value,
            inputDesc: '请输入金额',
            shoping: [
                {title: '国泰尊裕优化A', img: require('../assets/shopping/timg-4@2x.png'), rate: [{num: 1.038, desc: '单位净值'},{num: 1.038, desc: '单位净值'},{num: 1.038, desc: '单位净值'}], QR: require('../assets/shopingCard/二维码@2x.png')},
                {title: '国泰尊裕优化A', img: require('../assets/shopping/timg-4@2x.png'), rate: [{num: 1.038, desc: '单位净值'},{num: 1.038, desc: '单位净值'},{num: 1.038, desc: '单位净值'}], QR: require('../assets/shopingCard/二维码@2x.png')},
                {title: '国泰尊裕优化A', img: require('../assets/shopping/timg-4@2x.png'), rate: [{num: 1.038, desc: '单位净值'},{num: 1.038, desc: '单位净值'},{num: 1.038, desc: '单位净值'}], QR: require('../assets/shopingCard/二维码@2x.png')},
                {title: '国泰尊裕优化A', img: require('../assets/shopping/timg-4@2x.png'), rate: [{num: 1.038, desc: '单位净值'},{num: 1.038, desc: '单位净值'},{num: 1.038, desc: '单位净值'}], QR: require('../assets/shopingCard/二维码@2x.png')},
            ]
        }     
    },
    methods: {
        
    },
    watch: {
        value: function (val) {
            this.isShow = val;
        },
        isShow: function (val) {
            this.$emit('input', val);
        }
    }
}
</script>
<style scoped>
    /* 返回按钮过渡动画 */
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
    .wrapper {
        position: absolute;
        left: 0;
        top: 0;
        width: 10rem;
        height: 5.625rem;
        background-color: rgba(0, 0, 0, .5);
    }
    .card {
        position: absolute;
        padding-top: 0.8073rem;
        box-sizing: border-box;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 5.4531rem;
        height: 3.9427rem;
        background: url('../assets/shopingCard/Rectangle6@2x.png') no-repeat;
        background-size: contain;
    }
    .card__header {
        position: absolute;
        top: 0;
        width: 5.4531rem;
        height: 0.4896rem;
        background: url('../assets/call/Group36@2x.png') no-repeat;
        background-size: 100% 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .card__header__title {
        color: #fff;
        font-size: 0.1458rem;
    }
    .card__header__light {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 1.3333rem;
        height: 0.2396rem;
        background: url('../assets/call/Oval11@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    .card__header__close {
        position: absolute;
        right: 0.0885rem;
        top: 0.1771rem;
        width: 0.1354rem;
        height: 0.1354rem;
        background: url('../assets/common/Group8@2x.png') no-repeat;
        background-size: contain;
    }
    /* 购物车列表 */
    .call__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: #DFEFFF;
    }
    .call__list__item {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 0.2083rem;
        box-sizing: border-box;
        width: 4.5313rem;
        height: 0.5052rem;
        background: url('../assets/shopingCard/Rectangle8@2x.png') no-repeat;
        background-size: contain;
        margin-bottom: 0.1563rem;
    }
    .call__list__item:nth-of-type(2n) {
        background: url('../assets/shopingCard/Rectangle83@2x.png') no-repeat;
        background-size: contain;
    }
    .call__list__item__bar {
        position: absolute;
        left: 0;
        width: 0.0365rem;
        height: 0.5052rem;
        background: url('../assets/shopingCard/Rectangle82@2x.png') no-repeat;
        background-size: contain;
    }
    .call__list__item:nth-of-type(2n) .call__list__item__bar {
        background: url('../assets/shopingCard/Rectangle84@2x.png') no-repeat;
        background-size: contain;
    }
    .call__list__item__img {
        position: relative;
        z-index: 100;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 0.3698rem;
        height: 0.3698rem;
    }
    .call__list__item__img img {
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
    }
    /* 图片相框 */
    .call__list__item__img--bg {
        width: 0.3698rem;
        height: 0.3698rem;
        background: url('../assets/shopingCard/Group33@2x.png') no-repeat;
        background-size: contain;
    }
    /* 商品名称 */
    .call__list__item__title {
        font-size: 0.1042rem;
    }
    .call__list__item__data--num {
        font-size: 0.0938rem;
        line-height: .15rem;
    }
    .call__list__item__data--desc {
        font-size: 0.0625rem;
        line-height: .15rem;
    }
    /* 输入金额 */
    .call__list__item__input {
        color: #C3D4E7;
        font-size: 0.0729rem;
    }
    .call__list__item__input input{
        width: 0.5208rem;
        height: 0.1563rem;
        font-size: 0.0729rem;
        text-align: center;
        margin-right: 0.0521rem;
        color: #C3D4E7;
        background: url('../assets/shopingCard/Rectangle3@2x.png') no-repeat;
        background-size: contain;
    }
    .call__list__item:nth-of-type(2n) .call__list__item__input input{
        background: url('../assets/shopingCard/Rectangle31@2x.png') no-repeat;
        background-size: contain;
    }
    .call__list__item__input input::placeholder {
        color: #C3D4E7;
        line-height: 0.1563rem;
    }
    /* 二维码 */
    .call__list__item__data__QR img{
        width: 0.2083rem;
        height: 0.2083rem;
    }
    /* 删除订单 */
    .call__list__item__data__remove {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 0.5052rem;
        width: 0.25rem;
        background: linear-gradient(175deg,rgba(84,173,245,1) 0%,rgba(30,95,189,1) 100%);
    }
    .call__list__item:nth-of-type(2n) .call__list__item__data__remove {
        height: 0.5052rem;
        width: 0.25rem;
        background: linear-gradient(180deg,rgba(253,225,80,1) 0%,rgba(240,148,21,1) 100%);
    }
    /* 垃圾桶icon */
    .call__list__item__data__remove i {
        width: 0.1667rem;
        height: 0.1667rem;
        background: url('../assets/shopingCard/Group3@2x.png') no-repeat;
        background-size: contain;
    }

</style>



