<template>
    <div class="fund">
        <div class="fund__title">{{ this.title }}</div>
    </div>
</template>
<script>
export default {
    props: ['title']
}
</script>
<style scoped>
    .fund {
        width: 1.7604rem;
        height: 1.7604rem;
        background: url('../assets/market/fund/Group8@2x.png') no-repeat;
        background-size: 100% 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
    }
    .fund__title {
        font-weight: 500;
        color:#DFEFFF;
        text-shadow: 0rem 0rem 0.0469rem rgba(150,189,255,1);
    }
</style>



