<template>
    <transition name="fade">
        <div class="finan">
            <div class="finan__main" v-show="!giveData.detail">
                <ul class="finan__mainlist">
                    <li :class="['finan__mainlist__item', {activeItem: itemIdx === i}]" v-for="(item, i) in Market[5].finan" :key="i"
                     @click="curItem(i)">
                        <div class="finan__mainlist__leftct">
                            <div class="finan__mainlist__leftct-num">{{ item.per }}</div>
                            <div class="finan__mainlist__leftct-intro">预期年化收益率</div>
                        </div>
                        <div class="finan__mainlist__rightct">{{ item.name }}</div>
                    </li>
                </ul>
                <div class="finan__maincont">
                    <div class="finan__maincont__circle">
                        <div class="finan__maincont__circle-num">{{ Market[5].finan[itemIdx].per }}</div>
                        <div class="finan__maincont__circle-intro">预计年收益</div>
                    </div>
                    <div class="finan__maincont__title">{{ Market[5].finan[itemIdx].name }}</div>

                    <ul class="finan__maincont__wrap">
                        <li class="finan__maincont__wrap__child">
                            <div class="finan__maincont__wrap__child-title">起购金额</div>
                            <div class="finan__maincont__wrap__child-intro"><span class="child-style">{{ Market[5].finan[itemIdx].begin }}</span>万元</div>
                        </li>
                        <li class="finan__maincont__wrap__child">
                            <div class="finan__maincont__wrap__child-title">期限</div>
                            <div class="finan__maincont__wrap__child-intro"><span class="child-style">{{ Market[5].finan[itemIdx].limit }}</span>天</div>
                        </li>
                    </ul>
                    <div class="finan__maincont__detailbtn" @click="showDetail">查看详情</div>
                </div>
            </div>

            <transition name="fade">
                <finan-detail v-show="giveData.detail" :detail="giveData"></finan-detail>
            </transition>
        </div>
    </transition>     
</template>
<script>
import { mapGetters } from 'vuex'
import finanDetail from './market-financing-detail.vue'

export default {
    components: {
        finanDetail
    },
    computed: {
        ...mapGetters([
            'Market'
        ])
    },
    data() {
        return {
            itemIdx: 0,
            // detail: false,
            giveData: {
                detail: false
            }
        }
    },
    // $watch: {
    //     itemIdx: Number
    // },
    methods: {
        curItem(idx) {
            this.itemIdx = idx;
        },
        showDetail() {
            this.giveData.detail = !this.giveData.detail;
        }
    },
    mounted() {
        var finanArr = this.Market[5].finan;
        setInterval(() => {
            this.itemIdx++;
            if(this.itemIdx === 6) {
                this.itemIdx = 0;
            }            
        }, 5000);
    }
    // updated() {
    //     this.navIdx !== 5 ? !detail : null;
    //     console.log(this.navIdx)
    // }
}
</script>
<style>
    .finan {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        /* z-index: 1; */
        font-family: 'PingFangSC-Semibold';
        font-weight: 600;
        color: rgba(223,239,255,1);
    }
    .finan__main {
        position: absolute;
        top: 0.9896rem;
        left: 1.9115rem;
        box-sizing: border-box;
        width: 7.7552rem;
        height: 3.7448rem;
        background: url('../assets/market/metal/metalbg.png') no-repeat;
        background-size: contain;
    }

    /* 对6个列表绝对定位 */
    .finan__mainlist {
        position: relative;
    }
    .finan__mainlist__item {
        display: flex;

        position: absolute;
        width: 1.5208rem;
        height: 0.6354rem;
        box-sizing: border-box;
        padding: 0.1719rem 0.2448rem 0.1771rem 0.25rem;
        background-image: url('../assets/market/financing/left_frame1@2x.png');
        background-repeat: no-repeat;
        background-size: contain;
        text-align: center;
    }
    .finan__mainlist__item:nth-of-type(1) {
        left: 0.7188rem;
        top: 0.5938rem;
    }
    .finan__mainlist__item:nth-of-type(2) {
        left: 0.7188rem;
        top: 1.5365rem;
    }
    .finan__mainlist__item:nth-of-type(3) {
        left: 0.7188rem;
        top: 2.4583rem;
    }
    .finan__mainlist__item:nth-of-type(4) {
        right: 0.7188rem;
        top: 0.5938rem; 
    }
    .finan__mainlist__item:nth-of-type(5) {
        right: 0.7188rem;
        top: 1.5365rem;
    }
    .finan__mainlist__item:nth-of-type(6) {
        right: 0.7188rem;
        top: 2.4583rem;
    }

    /* 对点击的列表缩放 */
    .activeItem {
        transform: scale(1.2);
        background-image: url('../assets/market/financing/left_light_frame@2x.png');
    }

    
    /* 列表内部样式 */
    .finan__mainlist__leftct-num {
        font-size: 0.1354rem;
        font-family: 'DINPro-Bold';
        font-weight: bold;
        line-height: 0.1719rem;
    }
    .finan__mainlist__leftct-intro {
        font-size: 0.0573rem;
        font-family: 'PingFangSC-Medium';
        font-weight: 500;
        line-height: 0.0833rem;
    }
    .finan__mainlist__rightct {
        margin-left: 0.2031rem;
        max-width: 0.5208rem;
        font-size: 0.099rem;
        font-weight: 600;
        color: rgba(223,239,255,1);
        line-height: 0.1458rem;
        text-overflow: ellipsis;
    }

    /* 中间区域 */
    .finan__maincont {
        width: 2.0208rem;
        height: 2.5313rem;
        margin: 0 auto;
        margin-top: 0.5573rem;
        box-sizing: border-box;
        padding: 0.1875rem 0.2865rem 0 0.276rem;
        background: url('../assets/market/financing/sys_bg@2x.png') no-repeat;
        background-size: contain;
    }
    .finan__maincont__circle {
        width: 0.7448rem;
        height: 0.7448rem;
        margin: 0 auto;
        margin-bottom: 0.0365rem;
        box-sizing: border-box;
        padding-top: 0.224rem;
        background: url('../assets/market/financing/middle_circle@2x.png') no-repeat;
        background-size: contain;
        text-align: center;
    }
    .finan__maincont__circle-num {
        font-size: 0.1354rem;
        font-family: 'DINPro-Bold';
        font-weight: bold;
        line-height: 0.1719rem;
    }
    .finan__maincont__circle-intro {
        font-size: 0.0729rem;
        font-family: 'PingFangSC-Medium';
        font-weight: 500;
        line-height: 0.1042rem;
    }
    .finan__maincont__title {
        margin-bottom: 0.1823rem;
        text-align: center;
        font-size: 0.1042rem;
        line-height: 0.1458rem;
        font-weight: 600;
    }

    .finan__maincont__wrap {
        display: flex;
        justify-content: space-between;
        margin-bottom: 0.3229rem;
        font-family: 'PingFangSC-Medium';
    }
    .finan__maincont__wrap__child {
        width: 0.6719rem;
        height: 0.3333rem;
        background: url('../assets/market/financing/midle_frame@2x.png') no-repeat;
        background-size: contain;
        text-align: center;
        
    }
    .finan__maincont__wrap__child-title {
        margin-bottom: 0.0781rem;
        font-size: 0.0573rem;
        line-height: 0.0938rem;
    }
    .finan__maincont__wrap__child-intro {
        font-size: 0.0677rem;
        line-height: 0.0938rem;
    }
    .child-style {
        font-size:0.1146rem;
        font-family: 'DINPro-Bold';
        font-weight: bold;
        line-height: 0.1458rem;
    }
    .finan__maincont__detailbtn {
        width: 1.0469rem;
        line-height: 0.2396rem;
        margin: 0 auto;
        background: url('../assets/market/financing/Group20@2x.png') no-repeat;
        background-size: contain;
        text-align: center;
        font-size: 0.0938rem;
    }
</style>



