<template>
    <div class="inter">
        <div class="inter__wrapper">
            <div class="inter__type__title">存款类型</div>
             <Select v-model="model1" style="width: 1.5104rem">
                <Option v-for="item in types" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            <!-- <input type="text"> -->
        </div>
        <div class="inter__wrapper">
            <div class="inter__type__title">存款金额</div>
            <input type="text">
        </div>
        <div class="inter__wrapper">
            <div class="inter__type__title">存款币种</div>
            <Select v-model="model2" style="width: 1.5104rem">
                <Option v-for="item in yans" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
        </div>
        <div class="inter__wrapper">
            <div class="inter__type__title">存款存入时间</div>
            <DatePicker type="date" placeholder="请选择时间" style="width: 1.5104rem"></DatePicker>
        </div>
        <div class="inter__wrapper">
            <div class="inter__type__title">存款取出时间</div>
            <DatePicker type="date" placeholder="请选择时间" style="width: 1.5104rem"></DatePicker>
        </div>
        <div class="inter__wrapper">
            <div class="inter__type__title">存款利率(%)</div>
            <input type="text">
        </div>
    </div>
</template>
<script>
export default {
   
    data() {
        return {
           types: [
                {
                    value: '人民币',
                    label: '人民币'
                },
                {
                    value: '美元',
                    label: '美元'
                },
              
            ],
            yans: [
                {
                    value: '1500元以下',
                    label: '1500元以下'
                },
                {
                    value: '3500元以下',
                    label: '3500元以下'
                },
            ],
            model1: '',
            model2: '',
        }
    },
}
</script>
<style scoped>
    .inter {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 0.2604rem;
    }
    .inter__wrapper {
        flex: 0 0 47%;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0.1771rem;
    }
    .inter__type__title {
        font-size: 0.1rem;
        color: #DFEFFF;
    }
    .inter__wrapper input {
        width: 1.5104rem;
        height: 0.1875rem;
        outline: 0;
        font-size: 0.0729rem;
        border: 0;
        color: #DFEFFF;
        background: url('../assets/calculator/Rectangle19Copy@2x.png') no-repeat;
        background-size: contain;
        box-sizing: border-box;
        padding: 0 .05rem;
    }
</style>




