<template>
    <div class="fund">
        <div class="fund__window">
            <search class="search"></search>
            <div class="senior" @click="closeSenior"></div>

            <div class="fund__window--wrapper">
                <div class="fund__window__item" v-for="(item, key) in FundProduct" :key="key" @click="proDet(key)">
                    <div class="fund__item__title">{{ item.title }}</div>
                    <div class="fund__item__data--big">
                        <div class="fund__item__data--big__rate">{{ item.unit.rate }}</div>
                        <div class="fund__item__data--big__title">{{ item.unit.title }}</div>
                    </div>
                    <div class="fund__item__data--small">
                        <div class="fund__item__data--small__item">
                            <div class="fund__item__data--small__item__desc">{{ item.total.rate }}</div>
                            <div class="fund__item__data--small__item__title">{{ item.total.title }}</div>
                        </div>
                        <div class="fund__item__data--small__item">
                            <div class="fund__item__data--small__item__desc">{{ item.type.desc }}</div>
                            <div class="fund__item__data--small__item__title">{{ item.type.title }}</div>
                        </div>
                        <div class="fund__item__data--small__item">
                            <div class="fund__item__data--small__item__desc">{{ item.range.rate }}</div>
                            <div class="fund__item__data--small__item__title">{{ item.range.title }}</div>
                        </div>
                    </div>
                    <div class="fund__item__car"></div>
                </div>
            </div>  
        </div>


        <!-- 高级弹窗 -->
        <transition name="fade">
            <div class="trans__senior" v-show="showSenior">
                <div class="trans__senior__close" @click="closeSenior()"></div>
                <div class="trans__senior__title">高级筛选</div>

                <div class="finan__senior__slider senior-ct">
                    <div class="finan__senior__slider-title senior-title">近一年区间净值增长率【-10%~65%】</div>
                    <Slider v-model="valueSlide" on-change.prevent range show-tip="never" style="width: 428px; transform: translateX(68px)"></Slider>
                    <ul class="senior__price__valct">
                        <li class="senior__price__val">负</li>
                        <li class="senior__price__val">0</li>
                        <li class="senior__price__val">10%</li>
                        <li class="senior__price__val">30%</li>
                        <li class="senior__price__val">50%</li>
                        <li class="senior__price__val">正</li>
                    </ul>                                       
                </div>

                <div class="trans__senior__protype senior-ct">
                    <div class="trans__senior__protype-title senior-title">基金类型</div>
                    <div class="senior-wrap">
                        <div :class="['trans__senior__prorype-item', 'senior-item', {activebg: type1 === i}]"
                         v-for="(item, i) in seniorchoice.type" :key="i"
                         @click="typeBtn1(i)">{{ item }}</div>
                    </div>
                </div>

                <div class="trans__senior__btnct">
                    <div class="trans__senior__btnct-cancel senior-btn" @click="closeSenior">取消</div>
                    <div class="trans__senior__btnct-confirm senior-btn" @click="closeSenior">确定</div>
                </div>
            </div>
        </transition>

        <!-- 基金产品详情 -->
        <transition name="fade">
            <div class="fund__details--wrapper" v-show="isShow">
                <div class="fund__details__box">
                    <div class="fund__details__box__chart">
                        <ve-line :data="chartData" :settings="chartSettings" :theme="option" width="2rem" height="2.7rem"></ve-line>
                    </div>
                    <div class="fund__details__box__desc">
                        <div class="fund__details__box__desc--title">{{ details.title }}</div>
                        <div class="fund__details__box__desc--wrapper">
                            <div class="fund__details__box__desc--item">基金公司名称：{{ details.company }}</div>
                            <div class="fund__details__box__desc--item">行内基金代码：{{ details.code }}</div>
                            <div class="fund__details__box__desc--item">成立时间：{{ details.Cdate }}</div>
                            <div class="fund__details__box__desc--item">类型：{{ details.type }}</div>
                            <div class="fund__details__box__desc--item">基金状态：{{ details.state }}</div>
                            <div class="fund__details__box__desc--item">币种：{{ details.currency }}</div>
                            <div class="fund__details__box__desc--item">份额累计净值：{{ details.netNum }}</div>
                            <div class="fund__details__box__desc--item">银河证券三年期基金星级评价：{{ details.grade }}</div>
                            <div class="fund__details__box__desc--item">数据截至日期：{{ details.Sdate }}</div>
                            <div class="fund__details__box__desc--item">近一月净值增长率：{{ details.Mrate }}</div>
                            <div class="fund__details__box__desc--item">近半年净值增长率：{{ details.Hrate }}</div>
                            <div class="fund__details__box__desc--item">近一年净值增长率：{{ details.Yrate }}</div>
                            <div class="fund__details__box__desc--item">净值日期：{{ details.Ndate }}</div>
                        </div>
                        <div class="fund__details__box__Ccltor">
                            <div class="fund__details__box__Ccltor__title">基金计算器</div>
                            <div class="fund__details__box__Ccltor--wrapper">
                                <div class="Ccltor--add--wrapper">
                                    <div class="fund__details__box__Ccltor--frame">
                                        <div class="add"></div>
                                    </div>
                                    <div class="fund__details__box__Ccltor--txt">手续费试算</div>
                                </div>
                                <div class="Ccltor--rate--wrapper">
                                    <div class="fund__details__box__Ccltor--frame">
                                        <div class="rate"></div>
                                    </div>
                                    <div class="fund__details__box__Ccltor--txt">收益试算</div>
                                </div>
                                  
                            </div>
                                
                        </div>
                    </div>
                    <div class="cover-line"></div>
                    <metalcode class="metalcode"></metalcode>   
                    <div class="fund__details__box__close" @click="closeDetails"></div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
import search from './search.vue'
import { mapGetters } from 'vuex'
import metalcode from './market-metal-code.vue'
import VeLine from 'v-charts/lib/line.common'

export default {
    computed: {
        ...mapGetters([
            'FundProduct'
        ])
    },
    // props: ['title']
    components: {
        search,
        metalcode,
        've-line': VeLine,
    },
    data() {
        return {
            chartSettings: {
                stack: { '用户': ['访问用户', '下单用户'] },
                area: true,
            },
            isShow: false,
            // 产品详情
            details: {},
            showSenior: false,
            type1: -1,
            seniorchoice: {
                type: ['股票型', '混合型','领券型','其他型','货币市场型','QDII型','FOF型']
            },
            valueSlide: [1, 6],
            chartData: {
                columns: ['日期', '下单用户'],
                rows: [
                    { '日期': '1/1', '访问用户': 1393, '下单用户': 1093, '下单率': 0.32 },
                    { '日期': '1/2', '访问用户': 3530, '下单用户': 3230, '下单率': 0.26 },
                    { '日期': '1/3', '访问用户': 2923, '下单用户': 2623, '下单率': 0.76 },
                    { '日期': '1/4', '访问用户': 1723, '下单用户': 1423, '下单率': 0.49 },
                    { '日期': '1/5', '访问用户': 3792, '下单用户': 3492, '下单率': 0.323 },
                    { '日期': '1/6', '访问用户': 4593, '下单用户': 4293, '下单率': 0.78 }
                ]
            },
            option: {
                textStyle: { color: '#DFEFFF' },
                "color": [
                    // "#dd6b66",
                    // "#759aa0",
                    // "#e69d87",
                    // "#8dc1a9",
                    // "#ea7e53",
                    // "#eedd78",
                    // "#73a373",
                    // "#73b9bc",
                    "#7289ab",
                    // "#91ca8c",
                    // "#f49f42"
                ],
                valueAxis: {
                    axisLine: { show: false },
            
                },
            }
        }     
    },
    methods: {
        // 打开详情
        proDet(idx) {
            this.isShow = true;
            // 点击的产品详情数据
            this.details = this.FundProduct[idx].details;
            // console.log(this.details);
        },
        // 关闭详情
        closeDetails() {
            this.isShow = false;
        },
        closeSenior() {
            this.showSenior = !this.showSenior;
        },
        typeBtn1(i) {
            this.type1 = i;
        }
    }
}
</script>
<style scoped>
    .fund__window {
        /* display: flex;
        justify-content: center; */
        position: absolute;
        width: 7.5729rem;
        height: 3.9792rem;
        left: 2rem;
        top: 0.7604rem;
        background: url('../assets/market/fund/bj@2x.png') no-repeat;
        background-size: 100% 100%;
        box-sizing: border-box;
        padding: 0 0.35rem;
        padding-top: 0.8073rem;
    }
    .search {
        position: absolute;
        top: 0.2708rem;
        left: 4.9271rem;
    }
    /* 高级 */
    .senior {
        position: absolute;
        top: 0.3021rem;
        left: 6.5417rem;
        width: 0.5208rem;
        height: 0.2292rem;
        background: url('../assets/market/fund/Group47@2x.png') no-repeat;
        background-size: 100% 100%;
    }

    .fund__window--wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        /* width: 6.6927rem; */
        height: 2.8646rem;
        overflow-y: auto;
    }
    .fund__window--wrapper::-webkit-scrollbar {
        display: none;
    }
    .fund__window__item {
        position: relative;
        width: 1.099rem;
        height: 1.474rem;
        background: url('../assets/market/fund/small_window_recommed@2x.png') no-repeat;
        background-size: 100% 100%;
        margin: 0 0.1302rem;
        margin-bottom: 0.2604rem;
        color: #DFEFFF;
        
    }
    .fund__item__car {
        position: absolute;
        top: 1.1667rem;
        left: 0.1146rem;
        width: 0.7969rem;
        height: 0.1823rem;
        background: url('../assets/market/fund/Group4@2x.png') no-repeat;
        background-size: 100% 100%;
    }
    /* 产品标题 */
    .fund__item__title {
        font-size: 0.0833rem;
        padding-left: 0.5rem;
        line-height: 0.1823rem;
    }
    .fund__item__data--big {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0.1302rem 0;

    }
    .fund__item__data--big__rate {
        font-size: 0.1302rem;
    }
    .fund__item__data--big__title {
        font-size: 0.0729rem;
    }

    .fund__item__data--small {
        display: flex;
        justify-content: space-around;
        padding: 0 0.03rem 0 0.05rem;
        box-sizing: border-box
    }
    .fund__item__data--small__item {
        display: flex;
        flex-direction: column;
        /* justify-content: center; */
        text-align: center;
        font-size: 0.0625rem;
    }
    .fund__item__data--small__item__title {
        line-height: 0.1823rem;
    }

    /* 产品详情弹窗 */
    .fund__details--wrapper {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        width: 10rem;
        height: 5.625rem;
        background-color: rgba(0, 0, 0, .5);
    }
    .fund__details__box {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 7.3646rem;
        height: 3.5573rem;
        background: url('../assets/market/invest/Groupbg.png') no-repeat;
        background-size: 100% 100%;
        box-sizing: border-box;
        padding: 0 0 0.1042rem 0.2448rem;
        display: flex;
        align-items: center;
    }
    /* 图表 */
    .fund__details__box__chart {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 2.0625rem;
        height: 2.9427rem;
        background: linear-gradient(180deg,rgba(39,57,142,1) 0%,rgba(7,18,71,1) 100%);
        /* margin-top: 0.3281rem; */
    }

    .fund__details__box__close {
        position: absolute;
        top: 0.0729rem;
        right: 0.0833rem;
        width: 0.401rem;
        height: 0.401rem;
        background: url('../assets/market/fund/inside_close@2x.png') no-repeat;
        background-size: contain;
    }

    .fund__details__box__desc {
        padding: 0 0.2188rem;
        /* padding-top: 0.3281rem; */
        color: #DFEFFF;
        /* height: 1.7031rem; */
        width: 3.3854rem;
    }
    .fund__details__box__desc--title {
       
        font-size: 0.1563rem;
        line-height: 0.2188rem;
        text-shadow: 0 0 0.0417rem rgba(150,189,255,1);
        padding-bottom: 0.2188rem;
    }
    .fund__details__box__desc--wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        font-size: 0.0938rem;
        font-weight: 500;
        height: 1.4063rem;
    }
    .fund__details__box__desc--item:nth-of-type(2n-1) {
        padding-left: 0.2188rem;
    }
    .fund__details__box__desc--item:nth-of-type(1) {
        flex: 0 0 100%;
        padding-left: 0;
    }
    .fund__details__box__desc--item {
        box-sizing: border-box;
        flex: 0 0 50%;
        line-height: 0.1719rem;
    }
    .metalcode {
        box-sizing: border-box;
        margin-left: 0;
    }

    /* 基金计算器 */
    .fund__details__box__Ccltor__title {
        font-size: 0.125rem;
        line-height: 0.4167rem;
    }
    .fund__details__box__Ccltor--wrapper {
        display: flex;

    }
    .Ccltor--rate--wrapper,
    .Ccltor--add--wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .Ccltor--add--wrapper {
        padding-right: 0.1563rem;
    }
    .fund__details__box__Ccltor--frame {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 0.4792rem;
        height: 0.4792rem;
        background: url('../assets/market/fund/Group25@2x.png') no-repeat;
        background-size: contain;
    }
    .fund__details__box__Ccltor--frame .add {
        width: 0.25rem;
        height: 0.25rem;
        background: url('../assets/market/fund/计算器@2x.png') no-repeat;
        background-size: contain;
    }
    .fund__details__box__Ccltor--frame .rate {
        width: 0.25rem;
        height: 0.25rem;
        background: url('../assets/market/fund/收益计算@2x.png') no-repeat;
        background-size: contain;
    }
    .fund__details__box__Ccltor--txt {
        font-size: 0.0938rem;
        line-height: 0.2865rem;
    }
    .cover-line {
        width: 0.0156rem;
        height: 3.0365rem;
        margin-right: 0.0521rem;
        margin-top: 0.5rem;
        background: url('../assets/market/fund/cover-line.png') no-repeat;
        background-size: contain;
    }


    /* 高级 */
    .trans__senior {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 11;
        width: 3.349rem;
        height: 5.625rem;
        box-sizing: border-box;
        padding-left: 0.1667rem;
        background: url('../assets/market/insurance/Group8@2x.png') no-repeat;
        background-size: contain;
        text-align: center;
        color:rgba(255,255,255,1);
    }
    .trans__senior__close {
        position: absolute;
        left: 0;
        top: 42%;
        width: 0.2604rem;
        height: 0.4688rem;
        background: transparent;
    }
    .trans__senior__title {
        width: 3.1771rem;
        height: 0.4896rem;
        margin-top: 0.1823rem;
        background: url('../assets/market/transfer/高级删选@2x.png') no-repeat;
        background-size: cover;
        font-size: 0.1875rem;
        line-height: 0.4323rem;
    }
    .senior-title {

        width: 2.3854rem;
        height: 0.3333rem;
        margin-left: 0.2396rem;
        background: url('../assets/market/transfer/right_bg@2x.png') no-repeat;
        background-size: contain;
        text-align: left;
        text-indent: 0.125rem;
        font-size: 0.125rem;
        line-height: 0.3333rem;
    }
    .senior-wrap {
        display: flex;
        flex-wrap: wrap;
        margin-left: 0.125rem;
        margin-top: 0.0781rem;
    }
    .senior-item {
        margin-top: 0.0938rem;
        margin-left: 0.2031rem;
        width: 0.6771rem;
        height: 0.1927rem;
        line-height: 0.1927rem;
        font-size: 0.1042rem;
        background-image: url('../assets/market/transfer/Rectangle10.png');
        background-repeat: no-repeat;
        background-size: contain;
    }
    .senior-btn {
        width: 1.0313rem;
        height: 0.3073rem;
        margin-left: 0.4792rem;
        line-height: 0.3073rem;
        text-align: center;
        font-size: 0.125rem;
        background-repeat: no-repeat;
        background-size: contain;
    }
    .activebg {
        background-image: url('../assets/market/transfer/Rectangle9.png');
    }

    /* 以上是可复用部分 */
    .trans__senior__protype {
        margin-top: 0.3802rem;
    }
    .trans__senior__btnct {
        display: flex;
        margin-left: -0.0156rem;
        margin-top: 0.5885rem;
    }
    .trans__senior__btnct-cancel {
        margin-left: 0.3125rem;
        background-image: url('../assets/market/transfer/Group212x.png');
    }
    .trans__senior__btnct-confirm {
        background-image: url('../assets/market/transfer/determine.png');
    }

    .finan__senior__slider {
        margin-top: 0.5521rem;
    }
    .finan__senior__slider-title {
        margin-bottom: 0.2396rem;
    }
    .senior__price__valct {
        display: flex;
        line-height: 0.1458rem;
        font-size: 0.1042rem;
        margin-top: 0.1563rem;
        margin-left: 0.3646rem;
    }
    .senior__price__val {
        margin-right: 0.2813rem;
    }
</style>



