<template>
    <div class="cirque--small">
        <img :src="this.shop.picPath" class="cirque--small__icon">
        <div class="cirque--small__title">{{this.shop.title}}</div>
    </div>
</template>
<script>
export default {
   
    data() {
        return {
          
        }
    },
    props: ['shop']
}
</script>
<style scoped>
    .cirque--small {
        width: 1.1458rem;
        height: 1.1458rem;
        background: url('../assets/index/right_small_icon1@2x.png') no-repeat;
        background-size: contain;
    }
    .cirque--small__icon {
        width: 0.2917rem;
        height: 0.2917rem;
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        top: 0.32rem;
    }
    .cirque--small__title {
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        top: 0.6719rem;
        color: #6CE6FF;
        font-size: 0.0729rem;
    }
</style>



