<template>
    <transition name="fade">
        <div class="invest">
            <div class="invest__main">
                <ul class="invest__mainside">
                    <li :class="['invest__mainside__item', {isActive: sideIdx === i}]" v-for="(item, i) in Market[4].invest" :key="i"
                     @click="curIntro(i)">{{ item.type }}</li>
                </ul>

                <ul class="invest__nav">
                    <li :class="['invest__nav__item', {isTitle: titleIdx === i}]" v-for="(item, i) in titleArr" :key="i"
                     @click="curTitle(i)">{{ item }}</li>
                </ul>

                <div class="invest__cont">
                    <ul class="invest__contcircle">
                        <li class="invest__contcircle__item item-1">
                            <div class="invest__contcircle__item__num">{{ Market[4].invest[titleIdx].yield }}</div>
                            <div class="invest__contcircle__item__intro">模态年化收益率：{{ Market[4].invest[titleIdx].yield }}</div>
                        </li>
                        <li class="invest__contcircle__item item-2">
                            <div class="invest__contcircle__item__num">{{ Market[4].invest[titleIdx].volatility }}</div>
                            <div class="invest__contcircle__item__intro">模态波动率：{{ Market[4].invest[titleIdx].volatility }}</div>
                        </li>                        
                    </ul>

                    <div class="invest__contline"></div>

                    <div class="invest__contintro">
                        <!-- 需替换成插件 -->
                        <div class="invest__contintro__bg"></div>
                        
                        <ul class="invest__contintro__list">
                            <li class="invest__contintro__list__item">
                                <div class="invest__icon"></div>
                                <span class="invest__text">货币类</span>
                            </li>
                            <li class="invest__contintro__list__item">
                                <div class="invest__icon"></div>
                                <span class="invest__text">海外其他类</span>
                            </li>
                            <li class="invest__contintro__list__item">
                                <div class="invest__icon"></div>
                                <span class="invest__text">权益类</span>
                            </li>
                            <li class="invest__contintro__list__item">
                                <div class="invest__icon"></div>
                                <span class="invest__text">固守类</span>
                            </li>                                                         
                        </ul>

                    </div>

                    
                    <div class="invest__discount">
                        <!-- 需用组件插件部分 -->
                        <div class="invest__discount__bg"></div>

                        <ul class="invest__discount__wrap">
                            <li :class="['invest__discount__wrap__item', {isDis: disIdx === i}]" v-for="(item, i) in linechartArr" :key="i"
                             @click="curDis(i)">{{ item }}</li>
                        </ul>
                    </div>
                </div>

                <div class="invest__detailbtn" @click="showDetail">查看配置详情</div>
            </div>


            <transition name="fade">
                <div class="invest__detailct" v-show="details">
                    <div class="invest__detail">
                        <div class="invest__detail__title">配置详情</div>

                        <ul class="invest__detail__cont">
                            <li class="invest__detail__cont__item" v-for="(item, i) in particulars" :key="i">
                                <div class="invest__detail__cont__itempercent">{{ item.percentage }}</div>
                                <img src="../assets/market/invest/packicon.png" class="invest__detail__cont__itemicon">
                                <div class="invest__detail__cont__itemtype">{{ item.series }}</div>
                                <div class="invest__detail__cont__itembtn">收益多</div>
                            </li>
                        </ul>

                        <div class="invest__detail__confirm" @click="showDetail">确定</div>

                        <div class="invest__detail__close" @click="showDetail"></div> 
                    </div>
                </div>
            </transition>
        </div>
    </transition>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
    computed: {
        ...mapGetters([
            'Market'
        ])
    },
    props: [
        'navIdx'
    ],
    data() {
        return {
            sideIdx: 0,
            titleIdx: 0,
            disIdx: 0,
            details: false,
            titleArr: ['短期（1-3年）', '中期（1-3年）', '长期（3年以上）'],
            linechartArr: ['三个月', '六个月', '近一年'],
            particulars: [
                {series: '建信现金添益A（00102092）', percentage: '22.17%比例'},
                {series: '建信现金添益A（00102092）', percentage: '22.17%比例'},
                {series: '建信现金添益A（00102092）', percentage: '22.17%比例'},
                {series: '建信现金添益A（00102092）', percentage: '22.17%比例'}
            ]
        }
    },
    methods: {
        curIntro(idx) {
            this.sideIdx = idx;
        },
        curTitle(idx) {
            this.titleIdx = idx;
        },
        curDis(idx) {
            this.disIdx = idx;
        },
        showDetail() {
            this.details = !this.details;
        }
    }
}
</script>
<style scoped>
    .invest {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        font-family: 'PingFangSC-Medium';
        font-size: 0.0729rem;
        color: rgba(223,239,255,1);
    }
    .invest__main {
        display: flex;
        position: absolute;
        top: 1.0208rem;
        left: 1.9844rem;
        box-sizing: border-box;
        width: 7.5313rem;
        height: 3.6146rem;
        padding-top: 0.6979rem;
        padding-left: 0.6458rem;
        padding-right: 0.5417rem;
        background: url('../assets/market/invest/investbg.png') no-repeat;
        background-size: contain;
    }

    .invest__mainside__item {
        width: 0.6979rem;
        height: 0.3125rem;
        line-height: 0.3125rem;
        margin-bottom: 0.1563rem;
        font-size: 0.125rem;
        text-align: center;
        background-image: url('../assets/market/invest/button1@2x.png');
        background-repeat: no-repeat;
        background-size: contain;
    }
    .isActive {
        background-image: url('../assets/market/invest/button_light@2x.png');
    }

    .invest__nav {
        position: absolute;
        top: 0.6563rem;
        left: 2.9792rem;
    }
    .invest__nav__item {
        display: inline-block;
        width: 0.8333rem;
        line-height: 0.2604rem;
        margin-right: 0.2604rem;
        text-align: center;
        font-size: 0.1042rem;
        background-image: url('../assets/market/invest/top_button@2x.png');
        background-repeat: no-repeat;
        background-size: contain;
    }
    .isTitle {
        background-image: url('../assets/market/invest/top_button_light@2x.png');
    }

    .invest__cont {
        display: flex;
        margin-top: 0.6146rem;
    }
    .invest__contcircle__item {
        margin-left: 0.3854rem;
    }
    .invest__contcircle__item__num {
        width: 0.5208rem;
        height: 0.5208rem;
        margin: 0 auto;
        margin-bottom: 0.0833rem;
        border: 0.0052rem solid rgba(71,202,208,1);
        border-radius: 50%;
        font-size: 0.125rem;
        line-height: 0.5208rem;
        text-align: center;
    }
    .invest__contcircle__item__intro {
        margin-bottom: 0.0833rem;
        font-size: 0.0938rem;
        font-family: 'PingFangSC-Regular';
        font-weight: 400;
        color: rgba(71,202,207,1);
        line-height: 0.1302rem;
    }

    .invest__contline {
        width: 0.0156rem;
        height: 1.5625rem;
        margin-left: 0.3333rem;
        border: 0.0078rem solid rgba(71,202,207,1);
    }

    .invest__contintro {
        display: flex;
        box-sizing: border-box;
        padding-top: 0.1302rem;
        padding-left: 0.3594rem;
    }
    .invest__contintro__list {
        margin-top: 0.1302rem;
        margin-left: 0.2083rem;
    }
    .invest__contintro__list__item {
        display: flex;
        margin-bottom: 0.0938rem;
    }
    .invest__contintro__bg {
        width: 0.8854rem;
        height: 0.8854rem;
        background: url('../assets/market/invest/circle@2x.png') no-repeat;
        background-size: contain;
    }
    .invest__icon {
        width: 0.0833rem;
        height: 0.0833rem;
        background-color: rgba(71,202,207,1);
    }
    .invest__text {
        margin-left: 0.0417rem;
        line-height: 0.0833rem;
    }

    .invest__detailbtn {
        position: absolute;
        top: 2.5833rem;
        left: 3.9531rem;
        width: 0.625rem;
        line-height: 0.1563rem;
        background: url('../assets/market/invest/button@2x.png') no-repeat;
        background-size: contain;
        text-align: center;
    }

    .invest__discount {
        box-sizing: border-box;
        padding-top: 0.1302rem;
        padding-left: 0.5521rem;
    }
    .invest__discount__bg {
        width: 1.3802rem;
        height: 0.901rem;
        background: url('../assets/market/invest/line-chart@2x.png') no-repeat;
        background-size: contain;
    }
    .invest__discount__wrap {
        margin-top: 0.2448rem;
        margin-left: 0.1094rem;
    }
    .invest__discount__wrap__item {
        display: inline-block;
        width: 0.3125rem;
        line-height: 0.1563rem;
        margin-left: 0.1042rem;
        text-align: center;
        background-image: url('../assets/market/invest/Rectangle15@2x.png');
        background-repeat: no-repeat;
        background-size: contain;
    }
    .isDis {
        background-image: url('../assets/market/invest/button_light1@2x.png');
    }


    /**
    *弹层
    */
    .invest__detailct {
        position: absolute;
        z-index: 1;
        top: 0;
        left:0;
        right: 0;
        height: 5.625rem;
        background: rgba(16,16,16,.5);
        font-family: 'PingFangSC-Regular';
        font-weight: 400;
    }
    .invest__detail {
        position: absolute;
        top: 0.9948rem;
        left: 1.8698rem;
        width: 6.2604rem;
        height: 3.6354rem;
        background: url('../assets/market/invest/Rectangle6@2x.png') no-repeat;
        background-size: contain;
    }
    .invest__detail__title {
        width: 6.2604rem;
        height: 0.4896rem;
        font-size: 0.1302rem;
        font-family: 'PingFangSC-Medium';
        font-weight: 500;
        line-height: 0.4896rem;
        text-align: center;
        background: url('../assets/market/invest/Group31@2x.png') no-repeat;
        background-size: cover;
    }

    .invest__detail__cont__item {
        position: relative;
        display: inline-block;
        width: 2.776rem;
        height: 0.9427rem;
        margin-top: 0.2083rem;
        margin-left: 0.2188rem;
        box-sizing: border-box;
        padding-left: 0.3594rem;
        padding-top: 0.3802rem;
        background: url('../assets/market/invest/Group6@2x.png') no-repeat;
        background-size: contain;
        font-size: 0.1042rem;
        line-height: 0.1458rem;
    }
    .invest__detail__cont__itempercent {
        position: absolute;
        top: 0.0313rem;
        left: 2.026rem;
    }
    .invest__detail__cont__itemicon {
        width: 0.4063rem;
        height: 0.4063rem;
    }
    .invest__detail__cont__itemtype {
        position: absolute;
        top: 0.3854rem;
        left: 1.026rem;
    }
    .invest__detail__cont__itembtn {
        position: absolute;
        top: 0.6406rem;
        left: 1.026rem;
        width: 0.3594rem;
        line-height: 0.1458rem;
        font-size: 0.0729rem;
        text-align: center;
        background: url('../assets/market/invest/收益多@2x.png') no-repeat;
        background-size: contain;
    }

    .invest__detail__confirm {
        width: 1.0313rem;
        line-height: 0.3125rem;
        margin: 0 auto;
        margin-top: 0.3854rem;
        font-size: 0.125rem;
        font-weight: 500;
        text-align: center;
        background: url('../assets/market/invest/Group20@2x.png') no-repeat;
        background-size: contain;
    }

    .invest__detail__close {
        width: 0.1354rem;
        height: 0.1354rem;
        position: absolute;
        top: 0.1823rem;
        right: 0.1615rem;
        background: url('../assets/market/invest/Group8@2x.png') no-repeat;
        background-size: contain;
    }
</style>



